// Screen reader text
.sr-only {
  position:absolute;
  left:-10000px;
  top:auto;
  width:1px;
  height:1px;
  overflow:hidden;
  .ie9-nb form & {
    position:static !important;
    left:0 !important;
    top:auto !important;
    width:auto !important;
    height:auto !important;
    overflow: visible;
    text-align: left;
  }
}

// Tooltips
.more-info-tooltip {
  $tooltip-background: $white;
  $tooltip-max-width: 16em;
  $tooltip-min-width: 300px;
  $tooltip-arrow-width: 8px;
  $tooltip-shadow: 0px 0px 4px silver;
  $tooltip-distance-from-item: 2.6em;
  $tooltip-arrow-distance-from-box: -1.3em;
  position: relative;
  &.more-info-tooltip--text {
    //color: $brand-green;
    @extend %a-effect;
  }
  .editions-matrix & {
    padding: 0;
    text-align: center;
    background: $tooltip-background;
    border-radius: 50%;
    border: 1px solid $base-border-color;
  }

  &:before {
    .editions-matrix & {
      color: $dark-gray;
      content: "i";
      padding: .5em;
      font-weight: bolder;
      font-style: italic;
      font-family: Georgia;
    }
  }


  &:focus,
  &:hover .more-info-tooltip__tip {
    opacity: 1;
    visibility: visible;
  }

  .more-info-tooltip__tip {
    @include position(absolute, null 0 $tooltip-distance-from-item -10.4em);
    transition: all 0.2s ease-in-out;
    background-color: $tooltip-background;
    background: #fff;
    border-radius: $base-border-radius;
    box-shadow: $tooltip-shadow;
    color: $dark-gray;
    font-size: 0.9em; // Make sure you use -webkit-backface-visibility: hidden; on the body element to prevent 1px nudging bugs.
    margin: 0 auto;
    max-width: $tooltip-max-width;
    min-width: $tooltip-min-width;
    opacity: 0;
    padding: 1em;
    text-align: center;
    visibility: hidden;
    z-index: 10;

    p {
      color: $base-font-color;
      line-height: $base-line-height;
      margin: 0;
    }

    &:after {
      @include position(absolute, null 0 null 0);
      border: $tooltip-arrow-width solid transparent;
      bottom: $tooltip-arrow-distance-from-box;
      color: $white;
      content: '';
      font-size: 1.4em;
      margin-left: -$tooltip-arrow-width;
      text-align: center;
      text-shadow: 0px 2px 2px silver;
    }
  }
  &.more-info-tooltip--text {
    &:hover {
      cursor: pointer;
    }
    .more-info-tooltip__tip {
      @include position(absolute, null 0 $tooltip-distance-from-item -10.6em);
    }
  }
}

.table-responsive {
  max-width: 100%;
  @include media($handheld-only) {
    overflow: scroll;
  }
}

.table-minimal {
  margin-top: 3em;
  &:first-child {
    margin-top: 0em;
  }
  min-width: 600px;
  $table-border-color: $base-border-color;
  $table-border: 1px solid $table-border-color;
  $table-padding: .75em 0em;
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;

  tbody {
    tr:first-child td {
      border-top: 2px solid $table-border-color;
    }
  }

  thead {
    th {
      border-bottom: 0px;
    }
  }

  tbody {
    td {
      border-bottom: 0px;
      border-top: 1px solid $table-border-color;
      line-height: $base-line-height;
      padding: $table-padding;
    }
  }
}
