/*------------------------------------*\
  #GLOBAL-STYLING
\*------------------------------------*/

html {
  -webkit-text-size-adjust: 100%;
}

/*
 * box-sizing best practice: http://bit.ly/1qk2tVR
 */

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  height: 100%;
  min-width: $container-min-width;
}

/*
 * Responsive images
 */

img {
  height: auto;
  max-width: 100%;
}

/*
 * Override new normalize declaration of "bolder".
 */

b,
strong {
  font-weight: bold;
}
