/*------------------------------------*    #HIDING
\*------------------------------------*/
/*
 * Hide visually and from screen readers:
 */
.u-hidden {
  display: none !important; }

/*
 * Hide only visually, but have it available for screen readers:
 * http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
 */
.u-visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

/*
 * Hide visually and from screen readers, but maintain layout:
 */
.u-invisible {
  visibility: hidden; }

/*
 * Hide text to allow background to show.
 */
.u-hide-text {
  overflow: hidden;
  text-indent: 101%;
  white-space: nowrap; }

