.product-section-navigation__container {
  border-bottom: 1px solid $light-gray;
  padding: 2em 0 0;
  @include span-columns(12 of 12);
  .platform_integrate & {
    border-color: $white;
  }
}
.product-section-navigation__title {
  float: left;
  h2 {
    font-weight: 300;
    text-transform: uppercase;
    font-size: modular-scale(2, $base-font-size - .2em);
  }
  .platform_integrate & {
    color: $white;
  }
}

.product-section-navigation__links {
  float: right;
  font-weight: 300;
  ul {
    display: block;
    font-size: modular-scale(0, $base-font-size - .2em);
    margin-top: 2em;
  }
  li {
    float: left;
    margin-left: 2em;
    &:first-child {
      margin-left: 0;
    }
  }
  a {
    color: $dark-gray;
    .platform_integrate & {
      color: $white;
    }
    &:hover {
      color: $medium-gray;
    }
  }
}

.platform-leaderboard__video {
  position: relative;
  margin-top: 3em;
  display: none;
  @include media($large-screen-up) {
    display: block;
  }

  .platform_integrate & {
    display: block;
  }
}
.platform-leaderboard__video__play-cta {
  text-align: center;
  a,
  button {
    @include primary-path-button;
    padding-left: 52px;
    position: relative;
  }
  .play-icon {
    border: 1px solid #435a1b;
    transition: border-color 100ms ease-in-out;
    border-radius: 50%;
    margin-right: 6px;
    display: inline-block;
    width: 24px;
    height: 24px;
    position: absolute;
    top: 11px;
    left: 20px;
    svg {
      width: 12px;
      height: 12px;
      margin-left: 2px;
      margin-top: 5px;
    }
    path {
      fill: #435a1b;
      transition: fill 100ms ease-in-out;
    }
  }
  button:hover .play-icon {
    border-color: #1e280c;
    cursor: pointer;
    path {
      fill: #1e280c;
    }
  }
}
.platform-leaderboard__video__demo-cta {
  text-align: center;
  color: $white;
  margin-top: 16px;
  font-size: .8em;
  .platform-leaderboard__video__iframe & {
    color: $dark-gray;
  }
}

.platform-leaderboard__video__iframe {
  position: absolute;
  top: -2px;
  left:50%;
  margin-left: -150px;
  width: 300px;
  visibility:hidden;
  opacity:0;
  transition:visibility 0s linear 0.2s,opacity 0.2s linear;
  &.js-video-playing {
    visibility:visible;
    opacity:1;
    transition-delay:0s;
  }

  @include media($medium-screen-up) {
    margin-left: -300px;
    width: 600px;
  }
  @include media($large-screen-up) {
    margin-left: -350px;
    width: 700px;
  }

  // Video Container Drop Shadow
  .fluid-width-video-wrapper {
    &:before, &:after {
      z-index: -1;
      position: absolute;
      content: "";
      bottom: 15px;
      left: 10px;
      width: 50%;
      top: 80%;
      max-width:300px;
      background: #777;
      -webkit-box-shadow: 0 15px 10px #777;
      -moz-box-shadow: 0 15px 10px #777;
      box-shadow: 0 15px 10px #777;
      -webkit-transform: rotate(-3deg);
      -moz-transform: rotate(-3deg);
      -o-transform: rotate(-3deg);
      -ms-transform: rotate(-3deg);
      transform: rotate(-3deg);
    }
    &:after {
      -webkit-transform: rotate(3deg);
      -moz-transform: rotate(3deg);
      -o-transform: rotate(3deg);
      -ms-transform: rotate(3deg);
      transform: rotate(3deg);
      right: 10px;
      left: auto;
    }
  }
}

.platform-value-prop {
  padding: 2em 0;
  @include media($medium-screen-up) {
    padding: 3em 0;
  }

  h3 {
    font-size: modular-scale(1.5, $base-font-size);
    font-weight: 300;
    margin-top: 0;
  }
  p {
    margin-bottom: 2em;
    color: #81857e;
    font-weight: 300;
    font-size: .9em;
    line-height: 2;
  }
  .platform-value-prop__cta {
    margin-top: 0;
    a {
      @include skeleton-button--dark(#81857e);
      font-size: .8em;
    }
  }

  .platform-value-prop__container {
    @include media($medium-screen-up) {

    }
    @include media($large-screen-up) {
      width: percentage(1/2);
      float: left;
    }
    @include media($xl-screen-up) {

    }
  }

  .platform-value-prop__image {
    margin: 2em auto 0;
    width: percentage(1/1.5);
    @include media($large-screen-up) {
      margin-top: 0;
      width: percentage(1/2.5);
      text-align: center;
      float: right;
    }
  }

  &.platform-value-prop--integrate {
    color: $white;
    background: #6d9695;
    p {
      color: $white;
    }
    a {
      @include skeleton-button--white;
    }

    background: url('/assets/images/product/integrate.jpg') center center no-repeat;
    color: $white;
    p {color: $white !important;}
    h3 {
      @include media($xl-screen-up) {
        padding-right: 4em;
      }
    }
    a {
      @include skeleton-button--white;
    }
  }
  &.platform-value-prop--scale {
    .platform-value-prop__container {
      @include media($large-screen-up) {
        margin-top: -1em;
      }
      @include media($xl-screen-up) {
        margin-top: 2em;
      }
    }
  }
  &.platform-value-prop--safeguard {
    background: url('/assets/images/product/data.jpg') center center no-repeat;
    color: $white;
    p {color: $white !important;}
    h3 {
      @include media($xl-screen-up) {
        padding-right: 4em;
      }
    }
    a {
      @include skeleton-button--white;
    }
  }
  &.platform-value-prop--third-party {
    background-color: $background-gray;
  }
}

.integrations-leaderboard-background {
  background-color: tint($brand-blue, 20%);
  h1, p {
    color: $white;
  }
}

.product .platform-value-prop {
  p {
    color: $white;
  }
  &.platform-value-prop--aligned p,
  &.platform-value-prop--insight p,
  &.platform-value-prop--evolve p,
  &.platform-value-prop--picture p {
    color: $dark-gray;
  }
}

.product-leaderboard-background {
  background: url('/assets/images/product/LeanCoffee_lifestyle.jpg') top center no-repeat $background-gray;
  background-size: cover;
  h2 {
    color: $white !important;
  }
  ul a {
    color: $white !important;
    &:hover {
      color: shade($white, 15%);
    }
  }
}

.platform-leaderboard--product {
  h1 {
    color: $white;
  }
  padding: 6em 0;
  @include media($large-screen-up) {
    padding: 12em 0;
  }
  @include media($xl-screen-up) {
    padding: 15em 0;
  }
}
.platform-value-prop--picture {
  @include media($large-screen-up) {
    background: url('/assets/images/LK_Showcase_main.png') top center no-repeat;
    background-size: 50%, 50%;
    background-position: 100% 50%;
    padding: 14em 0;
  }
  @include media($xl-screen-up) {
    background: url('/assets/images/LK_Showcase_main.png') top center no-repeat;
    background-size: 40%, 40%;
    background-position: 90% 50%;
  }
}
.platform-value-prop--evolve {
  background-color: $background-gray;
  @include media($large-screen-up) {
    background: url('/assets/images/product/Evolve.png') top center no-repeat $background-gray;
    background-size: 60%, 60%;
    background-position: 130% 50%;
    padding: 14em 0;
  }
  @include media($xl-screen-up) {
    background: url('/assets/images/product/Evolve.png') top center no-repeat $background-gray;
    background-size: 50%, 50%;
    background-position: 107% 50%;
  }
}
.platform-value-prop--context {
  background: url('/assets/images/product/Work_in_context.jpg') bottom center no-repeat;
  color: $white;
  @include media($medium-screen-up) {
    background: url('/assets/images/product/Work_in_context.jpg') center center no-repeat;
  }
}
.platform-value-prop--insight {
  background-color: $background-gray;
  @include media($large-screen-up) {
    background: url('/assets/images/product/Business_in_sight.png') top center no-repeat $background-gray;
    background-size: 60%, 60%;
    background-position: 130% 50%;
    padding: 14em 0;
  }
  @include media($xl-screen-up) {
    background: url('/assets/images/product/Business_in_sight.png') top center no-repeat $background-gray;
    background-size: 40%, 45%;
    background-position: 95% 50%;
  }
}

.platform-value-prop--aligned {
  color: $dark-gray;
  @include media($large-screen-up) {
    background: url('/assets/images/product/Stay_transparent.png') top center no-repeat;
    background-size: 80%, 100%;
    background-position: 230% 50%;
    padding: 14em 0;
  }
  @include media($xl-screen-up) {
    background: url('/assets/images/product/Stay_transparent.png') top center no-repeat;
    background-size: 70%, 70%;
    background-position: 170% 50%;
  }
}
.platform-value-prop--anytime {
  background: url('/assets/images/product/Anytime_anywhere.jpg') top right no-repeat;
  color: $white;
  @include media($medium-screen-up) {
    background: url('/assets/images/product/Anytime_anywhere.jpg') top center no-repeat;
  }
}

.platform-value-prop--api {
  @include media($large-screen-up) {
    background: url("/assets/images/product/OpenAPI.png") right center no-repeat;
    background-size: 90%;
    background-position: 300% 0;
  }
  @include media($xl-screen-up) {
    background: url("/assets/images/product/OpenAPI.png") right center no-repeat;
    background-size: 70%;
    background-position: right center;
  }
}

.platform-value-prop--demo-cta {
  padding: 3em 0 5em;
  text-align: center;
  h4 {
    font-weight: 300;
  }
  a{
    @include primary-path-button;
  }
}

.product-demo-leaderboard {
  padding-bottom: 0em;
  .global-leaderboard__container__cta {
    margin: 2em 0;
  }
  .primary-path-cta {
    @include primary-path-button($brand-orange);
  }
}

.product-demo__video__container {
  text-align: center;
  color: $body-gray;
  font-weight: 300;
  .product-demo__video__iframe {
    margin-top: 1em;
    @include media($large-screen-up){
      margin-top: .5em;
      @include span-columns(9 of 12);
      @include shift(1.5 of 12);
    }
  }
}

.platform_integrate {
  .integrations-leaderboard-background {
    background: url("/assets/images/product/integrations_hero.jpg") center center no-repeat;
    color: #ffffff;
    margin-bottom: 2em;
  }

  .platform-value-prop {
    p {
      font-size: 1em;
      font-weight: 400;
      line-height: 1.8;

      color: #555851;
    }
    &.integrations-main-content {
      p {
        margin-bottom: 2em;
      }
    }
  }

  .platform-value-prop__container {
    @include media($medium-screen-up) {

    }
    @include media($large-screen-up) {
      width: percentage(1.4/2.5);
      float: left;
    }
    @include media($xl-screen-up) {

    }
  }

  .platform-value-prop__image {
    margin: 0 auto;
    width: 100%;
    @include media($large-screen-up) {
      width: percentage(.95/2.5);
      text-align: center;
      float: right;
    }
  }
  .integrations-logos-before {
    padding: 20px 0 0;
    @include media($large-screen-up) {
      padding: 85px 0 0;
    }
    h1 {
      font-size: modular-scale(2, $base-font-size);
      margin: 0;
      font-weight: 400;
    }
  }
  .integrations-logos {
    .module {
      border-bottom: 1px solid #d7d7d7;
      margin-bottom: 2em;
      margin-top: 0;
    }
    .platform-value-prop__container {
      @include media($large-screen-up) {
        width: 35%;
      }
    }
    .platform-value-prop__image {
      @include media($large-screen-up) {
        width: 64%;
      }
      .category-logos {
        width: 100%;
        margin: 2em 0 0;
        padding: 0;
      }
    }
    h3 {
      text-transform: uppercase;
      font-weight: 700;
      font-size: 1.1em;
    }
    p {
      font-size: .9em;
      width: 90%;
    }
  }
}

.platform-leaderboard--integrations {
  @include media($large-screen-up) {
    padding: 7em 0 9.5em;
  }
  .platform-leaderboard__heading {
    @include media($large-screen-up){
      @include span-columns(6 of 12);
      padding-right: 20px;
      padding-top: 1em;
    }

    @include media($xl-screen-up) {
      padding-right: 80px;
      padding-top: 3em;
    }
  }
  .platform-leaderboard__video {
    margin-top: 2em;
    @include media($large-screen-up){
      margin-top: 0;
      @include span-columns(6 of 12);
    }
  }
  span.h1 {
    font-weight: 500;
    font-size: modular-scale(2, $base-font-size + .1em);
    line-height: 1.3;
    @include media($large-screen-up) {
      font-size: modular-scale(3, $base-font-size + .1em);
    }
  }
  p {
    margin-top: 1.5em;
  }
  a {
    @include primary-path-button;
  }
}

.platform-value-prop__list--check {
  font-weight: 500;
  margin-top: 2.6em;
  margin-bottom: 3em;
  font-size: 1.05em;
  @include media($medium-screen-up) {
    font-size: 1em;
  }
  @include media($large-screen-up) {
    padding-right: 80px;
    font-size: 1.05em;
  }

  li {
    background: url('/assets/images/icons/check_outline_icon.svg') left center no-repeat;
    background-size: 46px 46px;
    padding-left: 65px;
    margin-bottom: 1.6em;
    @include media($small-screen-up) {
      margin-left: 8%;
      margin-right: 10%;
    }
    @include media($medium-screen-up) {
      min-height: 35px;
      background-size: 35px 35px;
      padding-left: 50px;
      padding-top: 6px;
      margin-left: 0;
      margin-right: 0;
    }
    @include media($large-screen-up) {
      background: url('/assets/images/icons/check_outline_icon.svg') left center no-repeat;
      padding-top: 0px;
      background-size: 42px 42px;
      padding-left: 70px;

    }
    @include media($xl-screen-up) {
      padding-right: 36px;
    }
  }
}

.platform-value-prop--integrations-cta {
  padding: 2em 0 2em !important;
  background-color: $background-gray;
  margin-top: 2em;
  @include media($medium-screen-up) {
    padding: 6em 0 3em !important;
  }
  .module {
    max-width: 900px;
    margin: 0 auto;
  }
  .platform-value-prop__container {
    @include media($small-screen-up) {
      width: 51%;
      float: left;
    }
  }
  .platform-value-prop__image {
    @include media($small-screen-up) {
      width: 49%;
      float: left;
    }
    img {
      margin: 2em 0 0;
      @include media($small-screen-up) {
        margin: 1em 0 0;
      }
      @include media($medium-screen-up) {
        margin: -3em 0 0;
      }
      text-align: left;
      float: left;
    }
  }
  h3 {
    font-weight: 700;
    font-size: 1.2em;
  }
  p {
    margin-bottom: 1.4em;
  }
  a {
    @include primary-path-button();
    @include media($medium-screen-up) {
      font-size: .95em;
    }
  }
}
