// $visual-grid: true;
// $visual-grid-color: red;

$grid-columns: 4;
$small-screen: 480px;
$medium-screen: 769px;
$large-screen: 1025px;
$xl-screen: 1200px;

$small-screen-up: new-breakpoint(min-width $small-screen 4);
$medium-screen-up: new-breakpoint(min-width $medium-screen 8);
$large-screen-up: new-breakpoint(min-width $large-screen 12);
$xl-screen-up: new-breakpoint(min-width $xl-screen 12);
$mobile-only: new-breakpoint(max-width $large-screen - 1);
$handheld-only: new-breakpoint(max-width $medium-screen - 1);
$mobile-tablet-only: new-breakpoint(max-width $large-screen - 1);

@mixin omega-reset($nth, $context: null) {
  @if $context == null { &:nth-child(#{$nth}) { margin-right: flex-gutter(); } }
  @else { &:nth-child(#{$nth}) { margin-right: flex-gutter($context); } }
  &:nth-child(#{$nth}+1) { clear: none; }
}
