%wrapper {
  @include outer-container();
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: $base-line-height;
  @include media($medium-screen-up) {
    @include outer-container($medium-screen);
  }
  @include media($large-screen-up) {
    @include outer-container($large-screen);
  }
  @include media($xl-screen-up) {
    @include outer-container($xl-screen);
  }
}

%full-width {
  @include span-columns(4);
  @include media($medium-screen-up) {
    @include span-columns(8);
  }
  @include media($large-screen-up) {
    @include span-columns(12);
  }
  @include media($xl-screen-up) {
    @include span-columns(12);
  }
}

.module {
  //@extend %full-width;
  @extend %wrapper;
}

.module--full-width {
  @include outer-container(100%);
}

// Site Header and Main Navigation
.site-header, .site-fixed-header {
  @include clearfix;
  @include media($large-screen-up) {
    padding-left: $base-line-height;
    padding-right: $base-line-height;
  }
  h1 {
    float: left;
  }
  .main-navigation {
    display: none;
    @include media($large-screen-up) {
      float: right;
      display: block;
    }
    li {
      @include media($large-screen-up) {
        float: left;
      }
    }
  }
}

