// Mobile Header/Nav Slideout
.sliding-panel-content {
  $sliding-panel-width: 280px;
  @include position(fixed, 0 0 0 auto);
  @include size($sliding-panel-width 100%);
  transform: translateX( $sliding-panel-width);
  transition: all 0.25s $ease-in-out-circ;
  background: $dark-gray;
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  z-index: 1000;
  &.is-visible {
  	transform: translate3d(0,0,0);
  }

  padding-top: 46px;
}

.sliding-panel-fade-screen {
  @include position(fixed, 0 0 0 0);
  transition: all 0.15s ease-out 0s;
  background: #000;
  opacity: 0;
  visibility: hidden;
  z-index: 999;
  transform: translate3d(0,0,0);
  &.is-visible {
    opacity: 0.3;
    visibility: visible;
  }
}

body.js-mobile-nav-open {
  @include position(fixed, 0 0 0 0);
}

// Hamburger
#mobile-nav-toggle {
  display: none;
  visibility: hidden;
  @include media($mobile-only) {
    display: inline-block;
    visibility: visible;
    z-index: 99999 !important;
    position: absolute;
    top: 8px;
    right: 12px;
    color:white;
  }
}

.menu-open{
  display:none !important;
  visiblity: hidden !important;
}

.hamburger{
  $width:25px;
  $height:3px;
  width:$width;
  height:$height;
  background: $dark-gray;
  .js-mobile-nav-open & {
    background: $white;
  }
  .index &, .seo &, .ppc &, .kanban &, .project-management &, .agile &, .solutions & {
    background: $white;
  }
  display:block;
  position:absolute;
  top:50%;
  left:50%;
  margin-left:-$width/2;
  margin-top:-$height/2;
  transition: transform 200ms;
}

$hamburger-spacing:8px;

.hamburger-1{
  transform: translate3d(0,-$hamburger-spacing,0);
}
.hamburger-2{
  transform: translate3d(0,0,0);
}
.hamburger-3{
  transform: translate3d(0,$hamburger-spacing,0);
}
.menu-open:checked+.menu-open-button{
  .hamburger-1{
    transform: translate3d(0,0,0) rotate(45deg);
  }
  .hamburger-2{
    transform: translate3d(0,0,0) scale(0.1,1);
  }
  .hamburger-3{
    transform: translate3d(0,0,0) rotate(-45deg);
  }
}

.menu-open-button{
  border-radius:100%;
  width:40px;
  height:40px;
  z-index:2;
  cursor:pointer;
  margin: 0;
  &:hover {
    cursor:pointer;
  }
  &:focus {
    outline: 0;
  }
}


.js-mobile-navigation {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 1em;
  padding: 0 15px 0 30px;
  > li {
    margin-top: .4em;
    &:first-child {
      margin-top: 0;
    }
  }
  a {
    color: $medium-gray;
    &:hover, &:active {
      color: tint($medium-gray, 70%);
    }
  }

  ul {
    text-transform: none;
    margin-left: 18px;
  }

  .dropdown-menu {
    > a {
      &:after {
        content: "\25BE";
        margin: -5px 0 0 5px;
      }
    }
    ul {
      margin-bottom: 1em;
      font-weight: 300;
      li {
        margin-top: .3em;
        &:first-child {
          //margin-top: 0;
        }
      }
    }
  }
  .site-header & .trial-signup-button {
    margin-top: 1.7em;
  }
  .site-header & .login-button {
    margin-left: 0em;
    margin-top: 1em;
  }
}
