/**
 * Magnific
 */
.mfp {
  $close-button-size: 24px;

  &-bg {
    opacity: .6;
    z-index: 99998;
  }

  .mfp-close-btn-in &-close {
    background-color: black;
    border-radius: 50%;
    color: white;
    height: $close-button-size;
    font-size: $close-button-size;
    line-height: 1.1;
    opacity: 1;
    position: absolute;
    right: -#{$close-button-size/2};
    top: -#{$close-button-size/2};
    width: $close-button-size;
  }

  &-container {
    min-width: 320px;
    padding-left: $close-button-size/2 + 4;
    padding-right: $close-button-size/2 + 4;
  }

  &-popup--white {
    background: #FFF;
    border-radius: 10px;
    padding: ms(6);
    margin: ms(3) auto;
    max-width: 500px;
    position: relative;
    width: auto;

    @include large-mobile {
      padding: ms(8);
    }

    @include small-tablet {
      padding: ms(10);
    }

    @include tablet {
      padding: ms(12);
    }

    p:last-of-type {
      margin-bottom: 0;
    }
  }

  &-wrap {
    z-index: 99999 !important;
  }
}


/**
 * Move-horizontal effect
 */
.mfp-move-horizontal {

  /* start state */
  .mfp-with-anim {
    opacity: 0;
    transition: all 0.3s;

    transform: translateX(-50px);
  }

  &.mfp-bg {
    opacity: 0;
    transition: all 0.3s;
  }

  /* animate in */
  &.mfp-ready {
    .mfp-with-anim {
      opacity: 1;
      transform: translateX(0);
    }
    &.mfp-bg {
      opacity: 0.8;
    }
  }

  /* animate out */
  &.mfp-removing {

    .mfp-with-anim {
      transform: translateX(50px);
      opacity: 0;
    }
    &.mfp-bg {
      opacity: 0;
    }

  }

}
