.solutions {
  .hero {
    background: url("/assets/images/hero_solutions_software_dev.jpg") center center no-repeat;
    background-size: cover;
    .hero__leaderboard__messaging {
      padding: 0em;
      @include media($large-screen-up) {
        @include span-columns(10);
        @include shift(1);
        padding: 5.5em 0 0;
      }
      @include media($xl-screen-up) {
        @include span-columns(9);
        @include shift(1.5);
      }
    }
    p {
      text-transform: uppercase;
      font-weight: 700;
      line-height: 1.1;
      margin-top: 0;
      font-size: modular-scale(3, ($base-font-size * .8));
      @include media($small-screen-up) {
        font-size: modular-scale(4, ($base-font-size * .9));
      }
      @include media($large-screen-up) {
        font-size: modular-scale(5, ($base-font-size * .9));
      }
    }
    h1 {
      text-transform: none;
      margin-bottom: .6em;
      font-size: .8em;
      font-weight: 300;
      @include media($small-screen-up) {
        font-size: 1.15em;
      }
    }
  }
}

.solutions_project-manager {
  .hero {
    background: url("/assets/images/solutions/software-dev/LK_solutions_radiate.jpg") center center no-repeat;
    background-size: cover;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: -moz-linear-gradient(top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 100%);
      background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0,0,0,0.7)), color-stop(100%, rgba(0,0,0,0.3)));
      background: -webkit-linear-gradient(top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 100%);
      background: -o-linear-gradient(top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 100%);
      background: -ms-linear-gradient(top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 100%);
      background: linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 100%);
    }
  }
}

.solution__blockquote {
  .tertiary-path-cta {
    font-size: .7em;
    @include media($small-screen-up) {
      font-size: .85em;
    }
  }
}

.solutions__img-block--radiate {
  background-image: url('/assets/images/hero_solutions_software_dev.jpg');
  height: 500px;
}

.solutions__img-block--improve {
  background-image: url('/assets/images/solutions/software-dev/LK_solutions_improve.jpg');
  height: 500px;
}

.solutions__img-block--improveFocus {
  background-image: url('/assets/images/solutions/it-ops/LK_solutions_improveFocus.jpg');
  height: 500px;
}

.solutions__img-block--stabilizeFlow {
  background-image: url('/assets/images/solutions/it-ops/LK_solutions_stabilizeFlow.jpg');
