.solutions__tabbable-templates {
  background-color: $background-gray;
  padding: 2em 0 0;
  text-align: center;
  margin-bottom: 2em;

  .section__title, .section__intro {
    padding: 0 25px;
  }
  .section__intro {
    @include media($medium-screen-up) {
      width: 490px;
      margin: 0 auto;
    }
  }
}

ul.accordion-tabs {
  margin-bottom: 0;
  margin-left: 0;
  list-style-type: none;
  ul {
    margin-bottom: 0;
    margin-left: 0;
    list-style-type: none;
  }
}

.accordion-tabs {
  @include clearfix;
  padding: 0;
  padding-bottom: 2em;
  @include media($medium-screen-up) {
    padding-bottom: 0em;
  }
  .tab-header-and-content {
    list-style: none;
    @include media($medium-screen-up) {
      display: inline;
    }
    .tab-link {
      font-weight: 400;
      font-size: .85em;
      display: block;
      padding-left: 2em;
      @include media($medium-screen-up) {
        display: inline-block;
      }
    }
    &:first-child {
      .tab-link {
        padding-left: 0em;
      }
    }
  }



  .is-active {
    color: $dark-gray;
  }

  .tab-content {
    display: none;
    width: 100%;
    border-top: 1px solid $light-gray;
    margin-top: 12px;
    padding-top: 2em;
    @include media($medium-screen-up) {
      float: left;
    }
  }
}


.modal {
  label {
    cursor: pointer;
    font-weight: normal;
    margin-bottom: 0;
  }

  .modal-state {
    display: none;
  }

  .modal-fade-screen { // overlay
    @include position(fixed, 0 0 0 0);
    background-color: rgba(0, 0, 0, 0.85);
    opacity: 0;
    transition: opacity 0.25s ease;
    visibility: hidden;
    z-index: 99999;

    @include media($large-screen) {
      padding-top: 2em;
    }
  }

  .modal-close {
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    color: #ffffff;
    background-color: $brand-red;
    padding: 4px 8px;
    border-radius: 0 0 0 50px;
    font-size: .8em;
    padding-right: 22px;
    padding-left: 16px;
    &:hover {
      color: shade(#ffffff, 12%);
    }
    &:after {
      content: "X";
      font-size: 1.5em;
      font-weight: bold;
      position: absolute;
      top: -1px;
      right: 4px;
    }
  }

  .modal-inner {
    margin: auto;
    overflow: auto;
    position: relative;
    top: 0.5em;
    transition: opacity 0.25s ease;
    max-width: 95%;
    max-height: 95%;
    width: auto;
    display: inline-block;
    @include media($medium-screen-up) {
      max-height: 90%;
      max-width: 80%;
    }

    @include media($large-screen-up) {
      max-width: 80%;
    }
  }

  .modal-state:checked + .modal-fade-screen {
    opacity: 1;
    visibility: visible;
  }
}

.modal-open {
  overflow: hidden;
}

.modal-closed {
  overflow: auto;
}

.modal-content {
  color: #ffffff;
  font-size: 120%;

  p {
    color: #ffffff;
    font-size: 1.1em;
    font-weight: 400;
  }
  img {
    max-height: calc(100vh - 225px);
  }
}

.solution-template__block {
  margin-bottom: 2em;
  @include media($medium-screen-up) {
    width: 32%;
    display: inline-block;
    vertical-align: top;
    margin-bottom: 2em;
  }
  img {
    margin: 0;
  }
  h4 {
    text-transform: uppercase;
    font-weight: 500;
    font-size: .95em;
  }
  p {
    font-size: .8em;
    line-height: 1.3;
    width: 80%;
    margin: 0 auto;
  }
}
