// Site Header and Main Navigation
.site-header, .site-fixed-header {
  .site-header__content {
    max-width: 1400px;
    margin: 0 auto;
  }
  z-index: 9999;
  position: absolute;
  top: 0;
  height: 62px;
  width: 100%;
  padding-left: 15px;
  @include media($large-screen-up) {
    padding-left: 15px;
    padding-right: 15px;
    height: 92px;
  }

  @include media($xl-screen-up) {
    padding-left: 30px;
    padding-right: 30px;
  }

  h1 {
    background: url('/assets/images/logo-leankit-by-planview-light.svg') center center no-repeat;
    display: block;
    margin: 0;
    margin-top: 16px;
    width: 100px;
    height: 30px;
    @include media($large-screen-up) {
      margin-top: 6px;
      width: 101px;
      height: 36px;
    }
    a {
      display: block;
      width: 100px;
      height: 30px;
      @include media($large-screen-up) {
        width: 101px;
        height: 36px;
      }
    }
    span {
      border: 0;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px;
    }
  }

  @include media($mobile-only) {
    //background: #222;
  }
}

.site-header {
  position: relative;
  background-color: $white;
  border-bottom: 1px solid $light-gray;
  .index &, .ppc &, .seo &, .kanban &, .agile &, .project-management &, .solutions &{
    position: absolute;
    background-color: transparent;
    border-bottom: 1px solid transparent;
  }
  h1 {
    background: url('/assets/images/logo-leankit-by-planview-dark.svg') center center no-repeat;
    .index &, .ppc &, .seo &, .kanban &, .agile &, .project-management &, .solutions &{
      background: url('/assets/images/logo-leankit-by-planview-light.svg') center center no-repeat;
    }
  }
  .ppc &--no-hero h1 {
    background: url('/assets/images/logo-leankit-by-planview-dark.svg') center center no-repeat;
  }

  .dropdown-menu {
    ul {
      @include media($large-screen-up) {
        background: $white;
      }
    }
  }

  @include media($large-screen-up) {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

///////////////
// Main Nav Styles
///////////////
.main-navigation {
  color: $white;
  font-size: .7em;
  ul {
    margin-top: 1.5em;
    li {
      text-transform: uppercase;
      margin-left: 3em;
      position: relative;
      &:first-child {
        margin-left: 0;
      }
    }
    .dropdown-menu {
      ul {
        box-shadow: 0 1px 20px rgba(0,0,0,0.15);
        max-height: 0;
        overflow: hidden;
        transition: max-height 400ms $ease-out-back;
        margin-top: 11px;
        width: 170px;
        position: absolute;
        left: -12px;
        padding: 0 0 0 16px;
        li {
          clear: both;
          margin: 10px 0;
          width: 100%;
          margin: 0;
          &:first-child {
            margin-top: 4px;
          }
          &:last-child {
            margin-bottom: 4px;
          }
          a {
            width: 100%;
            display: block;
            padding: 12px 0;
          }
        }
      }
      &:hover {
        ul {
          display: block;
          clear: both;
          max-height: 300px;
          transition: max-height 700ms $ease-out-back;
        }
      }
      > a {
        &:after {
          content: "\25BE";
          margin: -3px 0 0 5px;
        }
      }
    }
  }
  a {
    color: $dark-gray;
    letter-spacing: .5px;
    transition: all .1s linear;
    font-weight: 500;
    padding: 1em .2em;
    &:hover {
      color: tint($dark-gray, 25%);
    }
  }
  > ul > li > a {
    .index &,
    .ppc &,
    .seo &,
    .kanban &,
    .agile &,
    .project-management &,
    .solutions & {
      color: $white;
      &:hover {
        color: shade($white, 15%);
      }
    }
  }
}


//////////////
// Fixed Header Styles
//////////////
.site-fixed-header {
  color: $dark-gray;
  position: fixed;
  transition: margin-top 250ms $ease-in-out-circ;
  transform: translate3d(0,0,0);
  background: $white;
  border-bottom: 1px solid $light-gray;
  height: 68px;
  margin-top: -68px;

  h1 {
    margin-top: 15px;
    background: url('/assets/images/logo-leankit-by-planview-dark.svg') center center no-repeat;
  }

  .main-navigation {
    ul {
      margin-top: 2.3em;
    }
    &>ul>li>a {
      color: $dark-gray;
      &:hover {
        color: tint($dark-gray, 25%);
      }
    }
    .dropdown-menu {
      ul {
        background: $white;
      }
      &:hover {
        ul {
          border-bottom: 1px solid $light-gray;
        }
      }
    }
  }

  &.fixed {
    @include media($large-screen-up) {
      margin-top: 0;
    }
  }
  @include media($mobile-only) {
    display: none;
    visibility: hidden;
  }
}

.site-header, .site-fixed-header {
  ul > li.trial-signup-button,
  ul > li.login-button {
    margin-top: -10px;
    a {
      @include skeleton-button();
    }
  }
  ul > li.login-button {
    margin-left: 1.1em;
    a {
      @include skeleton-button($brand-orange);
    }
  }
}
