//KLC
.leankit-learn {
  .page-title {
    font-size: 2em;
    font-weight: 300;
  }
}

.learning-center-main-content__container {
  margin-top: 3em;
  display: block;
  float: left;
  margin-bottom: 2em;
}

.page-template-page-learning-center-php article {
  header {
    padding: 70px 1em 20px 1em;
    .container {
      @include media($large-screen-up) {
        width: 90%;
        margin: 0 auto;
      }
      @include media($xl-screen-up) {
        max-width:1400px;
      }

    }
    h1 {
      color: #ffffff !important;
      font-weight: 300;
      font-size: 2em;
      margin-bottom: 0em;
      @include media($large-screen-up) {
        font-size: 3em;
      }
      @include media($xl-screen-up) {
        font-size: 3.5em;
      }
    }
  }

  .featured-section {
    margin-top: 5em;
    @include span-columns(4 of 4);
    margin-bottom: 2em;
    @include media($medium-screen-up) {
      @include span-columns(8 of 8);
    }

    @include media($large-screen-up) {
      @include span-columns(4 of 12);
      margin-bottom: 0em;
      margin-top: 0em;
    }
    h1 {
      font-size: 1.8em;
      font-weight: 400;
    }
    .featured-short-description {
      padding: 0 3em;
      font-weight: 400;
    }
    .posts {
      padding: 0 1.5em 0 2em;
      margin: 0 auto;
      width: 350px;
      @include media($xl-screen-up) {
        padding: 0 3em;
      }
    }
    .cta {
      width: 90%;
      margin: 0 auto 1em;
      text-align: center;
      padding-bottom: 2em;
      font-size: .8em;
      font-weight: 400;
      @include media($xl-screen-up) {
        width: 90%;
        font-size: .9em;
      }
    }
    .btn {
    }
  }
}
.page-template-page-learning-center-php, .category {
  .fold-content {
    margin-top: 2em;
    display: block;
    float: left;
    .resource-main {
      @include span-columns(4 of 4);
      @include media($large-screen-up) {
        @include span-columns(8 of 12);
      }
    }
  }
  .resource-sidebar {
    @include span-columns(4 of 4);

    @include media($large-screen-up) {
      @include span-columns(4 of 12);
    }
    aside {
      padding-top: 0;
    }
  }
  .ebook-cta {
    width: 100%;
    float: left;
    background: $background-gray;
    .ebook-cta__image {
      @include span-columns(4 of 12);
      img {
        display: block;
      }
    }
    .ebook-cta__copy {
      margin-top: .5em;
      @include span-columns(8 of 12);
      @include media($xl-screen-up) {
        margin-top: 2em;
      }
    }
    .cta-link {
      @include skeleton-button--dark;
      padding: .75em 1.5em;
      font-size: .8em;
      margin-bottom: 1em;
    }
  }
  aside .aside-title, aside .aside-byline, aside .aside-description {
    color: $dark-gray;
  }

}
.category article.featured h1 {
  height: 42px;
  font-size: 1.3em;
  height: 68px;
  @include media($large-screen-up) {
    height: 76px;
  }
}
.category .page-header {
  margin-bottom: 0;
}
.category .page-header h1 {
  font-weight: 300;
  font-size: 1.4em;
  @include media($medium-screen-up) {
    font-size: 3em;
    font-size: 1.6em;
  }
}
.category .page-header p {
  font-size: 1em;

}

.category #topArticles {
  background-color: transparent;
  padding-top: 2em;
  padding-bottom: 2em;
  .col-md-4 {
    @include span-columns(8 of 8);
    @include media($large-screen-up) {
      @include span-columns(4 of 12);
    }
    .well {
      padding: 1em;
    }
    p {
      line-height: 1.4;
    }
  }
}

.global-footer {
  h4 {
    text-transform: uppercase;
    color: #969b93;
  }
}
