.index {
  .hero h1 {
    @include media($xl-screen-up) {
      font-size: modular-scale(5, $base-font-size);
      padding-right: 1em;
    }
  }
}

// Product Intro
.product-intro {
  background: linear-gradient(#f5f5f3, #d6d4d0);
  padding-top: 3em;
  padding-bottom: 4em;
  @include media($medium-screen-up) {
    padding-top: 3em;
  }
  @include media($large-screen-up) {
    padding-bottom: 6em;
  }
}

.product-intro__pitch {
  text-align: center;
  h3 {
    font-size: modular-scale(2, $base-font-size);
    margin: 0 0 .2em;
    font-weight: 400;
    text-transform: uppercase;
    @include media($large-screen-up) {
      font-size: modular-scale(3, $base-font-size - .2em);
    }
  }
  p {
    color: $medium-gray;
    font-size: 1.075em;
    margin-bottom: 1.5em;
  }
}

.product-intro__image {
  background: url('/assets/images/LK_Showcase_main.png') center center no-repeat;
  margin: 0 auto;
  min-height: 360px;
  width: 100%;
  background-size: 80%;
  background-position: center bottom;
  @include media($small-screen-up) {
    background-position: center bottom;
    height: 418px;
    width: 480px;
  }
  @include media($medium-screen-up) {
    background-position: left center;
    height: 488px;
    width: 768px;
  }
  @include media($large-screen-up) {
    height: 588px;
    width: 832px;
    background-position: center center;
    background-size: contain;
  }

  .product-intro__image__cta {
    text-align: center;
    margin-right: 0em;
    margin-top: 0em;
    @include media($small-screen-up) {
      //float: right;
      //margin-right: 1.5em;
      margin-top: 0em;
    }
    @include media($medium-screen-up) {
      float: right;
      margin-right: 2.5em;
      margin-top: 5.5em;
    }
    @include media($large-screen-up) {
      margin-right: -1em;
      margin-top: 7.5em;
    }
  }
  .primary {
    @include primary-path-button;
    margin-bottom: 1em;
  }
  .secondary {
    text-align: center;
    font-size: .9em;
    color: $medium-gray;
    a {
      color: $medium-gray;
      &:hover {
        color: shade($medium-gray, 15%);
      }
    }
  }
}

.product-intro__value-propositions {
  margin-top: 1em;
  .product-intro__value-proposition {
    @include span-columns(4 of 4);
    margin-top: 2em;
    text-align: center;
    &:first-child {
      margin-top: 0em;
    }
    @include media($small-screen-up) {
      margin-top: 0em;
      @include span-columns(1.3 of 4);
    }
    @include media($medium-screen-up) {
      margin-top: 0em;
      text-align: left;
      @include span-columns(2.66 of 8);
    }
    @include media($large-screen-up) {
      @include span-columns(4 of 12);
    }
    @include media($xl-screen-up) {
      @include span-columns(3.5 of 12);
      &:first-child {
        @include shift(.75);
      }
    }

    .product-intro__value-proposition__icon {
      width: 100%;
      margin: 0 auto;
      text-align: center;
      img {
        width: 20%;
      }
      @include media($small-screen-up) {
        width: 100%;
        margin: 0 auto;
        text-align: center;
        img {
          width: 30%;
        }
      }
      @include media($medium-screen-up) {
        float: left;
        width: 17%;
        margin: 0 0;
        text-align: center;
        img {
          width: 100%;
        }
      }
    }
    .product-intro__value-proposition__title {
      text-transform: uppercase;
      font-weight: 400;
      margin-bottom: .5em;
      color: $dark-gray;
      letter-spacing: 1px;
      font-size: .95em;
      margin-top: .7em;

      @include media($small-screen-up) {
        width: 100%;
        float: left;
        clear: both;
        font-size: .8em;
        text-align: center;
        margin-top: .3em;
      }
      @include media($medium-screen-up) {
        width: 81%;
        float: right;
        clear: none;
        text-align: left;
        margin-top: .7em;
      }
      @include media($large-screen-up) {
        font-size: .95em;
      }
    }
    .product-intro__value-proposition__copy {
      font-weight: 300;
      color: $body-gray;
      font-size: .9em;
      padding: 0 2em;
      @include media($small-screen-up) {
        width: 100%;
        float: left;
        clear: both;
        text-align: center;
        padding: 0 0;
        font-size: .7em;
      }
      @include media($medium-screen-up) {
        padding-right: 1em;
        font-size: .85em;
        width: 81%;
        float: right;
        clear: none;
        text-align: left;
      }
      @include media($large-screen-up) {
        font-size: .9em;
      }
    }
  }
}

.section--home {
  padding: 2em 0 0 0;
}

.section__title {
  h3 {
    margin-bottom: 2em;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
  }
}

// Lean Learning
.lean-learning__content-block {
  height: 400px;
  @include media($medium-screen-up) {
    float: left;
    display: block;
    width: percentage(1/2);
  }
  @include media($xl-screen-up) {
    width: percentage(1/3);
  }


  @include media($medium-screen-up) {
    &:nth-child(3n) {
      float: right;
    }
    &:nth-child(4n) {
      float: left;
    }
  }
  @include media($large-screen-up) {
    &:nth-child(3n) {
      float: left;
    }
    &:nth-child(4n) {
      float: left;
    }
  }
}


.lean-learning__content-block--article {
  background: #607d39;
  color: $white;
  position: relative;
  background: url('/assets/images/transformation_800x600.jpg') 0% 5% no-repeat;
  &.lean-learning__content-block--article--new-lean {
    background: url('/assets/images/NewLean_800x600.jpg') 0% 5% no-repeat;
  }
  &.lean-learning__content-block--article--baton {
    background: url('/assets/images/WatchBaton_800x600.jpg') 80% 25% no-repeat;
  }
}

.lean-learning__content-block--article__container {
  position: absolute;
  bottom: 1.5em;
  left: 1.5em;
}
.lean-learning__content-block--article__category {
  text-transform: uppercase;
  font-weight: 400;
  font-size: .9em;
}
.lean-learning__content-block--article__title {
  h1 {
    margin: .4em 0;
    text-transform: uppercase;
    font-size: modular-scale(3, $base-font-size - .25em);
    width: 80%;
    @include media($small-screen-up) {
      font-size: modular-scale(3, $base-font-size - .1em);
    }
    @include media($medium-screen-up) {
      width: 95%;
    }
    @include media($large-screen-up) {
      width: 80%;
    }
    line-height: 1;
    a {
      color: $white;
      &:hover {
        color: shade($white, 15%);
      }
    }
  }
}
.lean-learning__content-block--article__copy {
  padding-right: 3em;
  font-size: .9em;
  line-height: 1.3;
  width: 70%;
  @include media($medium-screen-up) {
    width: 90%;
  }
  @include media($large-screen-up) {
    width: 75%;
  }
}
.lean-learning__content-block--article__cta {
  a {
    @include skeleton-button--white;
    margin-top: 1.5em;
    text-transform: uppercase;
    font-size: .7em;
  }
}

.lean-learning__content-block--quote {
  background: black;
  color: $white;
  position: relative;
  text-align: center;
  &.lean-learning__content-block--quote--one {
    background-color: shade($brand-green, 10%);
  }
  &.lean-learning__content-block--quote--two {
    background-color: $dark-gray;
  }
  &.lean-learning__content-block--quote--three {
    background-color: shade($brand-green, 50%);
  }
}

.lean-learning__content-block--quote__container {
  width: 90%;
  margin: 0em auto 0;
  padding-top: 6em;
  background: url('/assets/images/icons/quote_icon_white.svg') center 3em no-repeat;
  background-size: 40px 40px;

  @include media($medium-screen-up) {
    margin: 0em auto 0;
  }
  .lean-learning__content-block--quote--one & {
    padding-top: 5em;
    background-position: center 2em;
    @include media($medium-screen-up) {
      margin: 0 auto 0;
    }
  }
  .lean-learning__content-block--quote--two & {
    padding-top: 5em;
    background-position: center 2em;
    @include media($medium-screen-up) {
      margin: 0em auto 0;
    }
  }
  a {
    color: #ffffff;
    &:hover {
      color: shade(#ffffff, 10%);
    }
  }
}

.lean-learning__content-block--quote__blockquote {
  line-height: 1.3;
  font-size: 1em;
  margin: 0 auto;
  font-weight: 300;
  @include media($small-screen-up) {
    width: 346px;
  }
}

.lean-learning__content-block--quote__headshot {
  margin: 1.5em 0 .5em;
  img {
    border-radius: 50%;
    width: 100px;
    height: 100x;
  }
}
.lean-learning__content-block--quote__name {
  font-weight: 700;
  text-transform: uppercase;
}
.lean-learning__content-block--quote__att-title {
  font-size: .8em;
}


// Why LeanKit
.why-leankit__intro {
  p {
    @extend %wrapper;
    padding: 2em 30px;
    text-align: center;
    font-weight: 300;
    font-style: normal;
    color: $body-gray;
    font-size: modular-scale(1, $base-font-size - .15em);
    border-top: 1px solid $light-gray;
    border-bottom: 1px solid $light-gray;
    @include media($small-screen-up) {}
    @include media($medium-screen-up) {}
    @include media($large-screen-up) {
      padding: 2em 15px;
      margin: 0 auto;
    }
    @include media($xl-screen-up) {
      //margin: 0 8em;
    }
    .about & {
      border: 0;
      padding: 0em 30px 2em;
      margin: 0 auto;
      @include media($small-screen-up) {}
      @include media($medium-screen-up) {}
      @include media($large-screen-up) {
        padding: 0em 12% 2em;
      }
      @include media($xl-screen-up) {}
    }
  }
}

.why-leankit__cta {
  text-align: center;
  margin-bottom: 2em;
  a {
    @include primary-path-button;
  }
}

.why-leankit__intro {
  margin-bottom: 2em;
}

.why-leankit__blocks {
  @extend %wrapper;
  margin-bottom: 2em;
  padding: 0;
}

.why-leankit__block {
  text-align: center;
  margin-bottom: 2em;
  @include media($small-screen-up) {
    @include span-columns(2);
    @include omega(2n);
  }
  @include media($medium-screen-up) {
    @include span-columns(4);
    @include omega(2n);
  }
  @include media($large-screen-up) {
    @include span-columns(4);
    @include omega-reset(2n, 12);
    @include omega(3n);
  }
}

.why-leankit__block__icon {
  img {
    width: 70px;
    height: 70px;
  }
}
.why-leankit__block__title {
  text-transform: uppercase;
  font-weight: 400;
  margin: .5em auto 1em;
  width: 90%;
  @include media($medium-screen-up) {
    width: 90%;
  }
  @include media($xl-screen-up) {
    width: 68%;
  }
}
.why-leankit__block__copy {
  font-size: .85em;
  color: $body-gray;
  font-weight: 300;
  width: 80%;
  margin: 0 auto;
  @include media($large-screen-up) {
    width: 83%;
  }
  @include media($xl-screen-up) {
    width: 73%;
  }
}

