.section--global {}

// Trustmarks
.global-trustmarks {
  background: url('/assets/images/LK_customer_background.jpg') center center no-repeat;
  background-size: cover;
  padding: 3em 0 6em;
}
.global-trustmarks__title {
  color: $white;
  margin-bottom: 41.053px;
  h3 {
    margin-bottom: .5em !important;
    font-size: 2.2em;
  }
  p {
    text-align: center;
    color: $white;
    font-size: .9em;
    @include media($medium-screen-up) {
      font-size: 1.2em;
    }
  }
  a {}
}

.global-trustmarks__logos {
  margin-bottom: -2em;
  margin-top: -25.053px;
  img {}
}

// Newsletter
.global-newsletter-subscribe {
  background: $deep-gray;
  padding: 4em 0;
  color: $white;
  text-align: center;
}
.global-newsletter-subscribe__cta {
  font-size: 1.3em;
  margin-bottom: 0;
  p {}
}
#mktoForm_148 {
  color: #fff !important;
}
.global-newsletter-subscribe__form {
  height: 130px;
  margin: 0 auto;
  width: 95%;
  @include media($medium-screen-up) {
    width: 80%;
  }
  @include media($large-screen-up) {
    width: 55%;
  }
  @include media($xl-screen-up) {
    width: 40%;
  }

  .global-newsletter-subscribe__form__panel--form {
    position: relative;
  }
  .global-newsletter-subscribe__form__panel--success {
    font-weight: 300;
    display: none;
  }
  input[type="text"] {
    float: left;
    height: 37px;
    background-color: transparent;
    border: 1px solid $medium-gray;
    &:hover {
      border-color: lighten($medium-gray, 10%);
    }
    &:focus {
      border-color: $white;
    }
    @include span-columns(2.5 of 4);
    @include media($small-screen-up) {
      @include span-columns(2.7 of 4);
    }
    @include media($medium-screen-up) {
      @include span-columns(6 of 8);
    }
    @include media($large-screen-up) {
      @include span-columns(8.8 of 12);
    }
  }

  input[type="submit"] {
    font-size: .7em;
    height: 37px;
    @include span-columns(1.5 of 4);
    @include media($small-screen-up) {
      @include span-columns(1.3 of 4);
    }
    @include media($medium-screen-up) {
      @include span-columns(2 of 8);
    }
    @include media($large-screen-up) {
      @include span-columns(3.2 of 12);
    }
  }
  .errorMessage {
    position: absolute;
    top: 6px;
    left: 4px;
    color: #ef625f;
    background-color: #3a3b38;
    padding: 2px 0 2px 3px;
    @include media($handheld-only) {
      top: 42px;
    }
  }
}
.global-newsletter-subscribe__disclaimer {
  @include span-columns(12 of 12);
  font-size: .8em;
  color: $medium-gray;
  margin-top: 1em;
  span {}
  a {
    color: $light-gray;
    &:hover {
      color: shade($light-gray, 10%);
    }
  }
}

// Footer
.global-footer {
  background: $dark-gray;
  color: $white;
  padding: 1em 0;
  @include media($medium-screen-up) {
    padding: 4em 0 2em;
  }
}
.global-footer__column {
  width: 100%;
  clear: both;
  margin-top: 1em;
  float: left;
  &:first-child {
    margin-top: 0;
  }
  ul {
    text-align: left;
    @include media($handheld-only) {
      text-align: center;
    }
  }
  li {
    @include media($handheld-only) {
      margin-right: 14px;
      margin-bottom: 8px;
      display: inline-block;
    }
  }
  @include media($medium-screen-up) {
    width: percentage(.8/5);
    margin-left: percentage(.2/5);
    clear: none;
    float: left;
    margin-top: 0em;
    &:first-child {
      margin-left: 0;
    }
    li {
      float: none;
      margin-right: 0;
      clear: both;
    }
  }
  @include media($large-screen-up) {
    width: percentage(.8/5);
    margin-left: percentage(.2/5);;
    float: left;
    &:first-child {
      margin-left: 0;
    }
  }
  a {
    color: $medium-gray;
    &:hover {
        color: tint($medium-gray, 15%);
    }
  }
}

.global-footer__heading {
  text-transform: uppercase;
  color: $medium-gray;

  h4 {
    font-size: 1em;
  }

}

.global-footer__logo {
  span {
    background: url('/assets/images/LK_logo_ver_onBlack_rgb.svg') center center no-repeat;
    display: block;
    margin: 0 auto;
    width: 90px;
    height: 112px;
    text-indent: -9999px;
  }
}

.global-footer__link-map {
  text-align: left;
  @include media($handheld-only) {
    text-align: center;
  }
}
.global-footer__link-map__title {

}
.global-footer__link-map__list {
  ul {
    font-size: .8em;
  }
  li {}
  a {}
}

.global-footer__social {}
.global-footer__social__title {h4 {}}
.global-footer__social__list {
  @include media($medium-screen-up) {
    margin-top: .7em;
    margin-left: -20px;
    width: 280px;
  }
  ul {
    text-align: center;
  }
  li {
    float: left;
    clear: none;

    margin-right: 4px;
    margin-bottom: 4px;
    @include media($handheld-only) {
      float: none;
      display: inline;
    }
    &:last-child {
      margin-left: 0;
    }
    a {
      display: block;
      @include media($handheld-only) {
        display: inline-block;
      }
      margin: 0;
      text-indent: -9999px;
      background-size: 14px 14px;
      background-repeat: no-repeat;
      background-position: center center;
      border-radius: 50%;
      background-color: #3c3e38;
      width: 32px;
      height: 32px;
      @include media($large-screen-up) {
        width: 26px;
        height: 26px;
        background-size: 12px 12px;
      }
      @include media($xl-screen-up) {
        width: 32px;
        height: 32px;
        background-size: 16px 16px;
      }
    }
  }
  .fb {
    a {
      background-image: url('/assets/images/icons/footer-social-facebook.png');
      &:hover {
        background-color: #3b5998;
      }
    }
  }
  .tw {
    a {
      background-image: url('/assets/images/icons/footer-social-twitter.png');
      &:hover {
        background-color: #4099FF;
      }
    }
  }
  .in {
    a {
      background-image: url('/assets/images/icons/footer-social-linkedin.png');
      &:hover {
        background-color: #007bb6;
      }
    }
  }
  .yt {
    a {
      background-image: url('/assets/images/icons/footer-social-youtube.png');
      &:hover {
        background-color: #e52d27;
      }
    }
  }
  .gp {
    a {
      background-image: url('/assets/images/icons/footer-social-google.png');
      &:hover {
        background-color: #dd4b39;
      }
    }
  }
}

.global-footer__contact-info {
  margin-top: 1em;
  display: block;
  float: left;
  font-size: .75em;
  color: $medium-gray;
  width: 100%;
  line-height: 1.4;
  @include media($medium-screen-up) {
    margin-left: -20px;
    width: 280px;
  }
  .vcard {}
  .fn.org {}
  .organization-name {}
  .adr {}
  .street-address {}
  .locality {}
  .region {}
  .postal-code {}
  .tel {}
  .type {}
  .value {}
}

.global-footer__colophon {
  @extend %full-width;
  clear: both;
  display: block;
  text-align: center;
  margin-top: 4em;
  font-size: .8em;
  color: $medium-gray;

  .global-footer__link-map__list {
    display: block;
    width: 100%;
    text-align: center;
    clear: both;
    font-size: .9em;
    @include media($small-screen-up) {
      font-size: 1em;
    }
    ul {
      margin: 0 auto;
    }
    li {
      margin-left: 1em;
      display: inline-block;
      &:first-child {
        margin-left: 0;
      }
    }
  }
  a {
    color: shade($medium-gray, 30%);
    &:hover {
      color: shade($medium-gray, 10%);
    }
  }
  span {
    clear: both;
    display: block;
  }
}
