.login-section-navigation {}
.login-section-navigation__container {}
.login-section-navigation__title {
  h2 {}
}
      
.login-business-container {
  padding: 3em 0;
}
  .login-business__guidance {
    margin-bottom: 2em;
    @include media($large-screen-up) {
      @include span-columns(7 of 12);
      margin-bottom: 0em;
    }
    h3 {}
    p {}
  }
  .login-business-form {
    input[type="submit"] {
      @include skeleton-button--dark;
    }
  }
      
  .login-trial-path {
    @include media($large-screen-up) {
      @include shift(1);
      @include span-columns(4 of 12);
      margin-top: 2em;      
    }
  }
    .login-trial-path__callout {
      h3 {
        color: $brand-green;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 1.5em;
      }
      p {
        margin-bottom: .8em;
        .free {
          text-transform: uppercase;
          font-weight: 500;
        }
      }
    }
  .login-trial-path__callout__cta {
    a {
      @include primary-path-button;
    }
    margin-bottom: 1em;
  }
    .login-trial-path__callout__disclaimer {
      font-size: .7em;
      font-weight: 300;
      font-style: italic;
      padding: 0 4em;
    }
    
    

.sign-up-business-container {
  padding: 3em 0 4em;
}
.sign-up-business-form {
  margin-bottom: 2em;
  @include media($large-screen-up) {
    @include shift(.5);
    @include span-columns(4 of 12);
    margin-bottom: 0em;
  }
  label {
    font-size: .8em !important;
  }
  input[type="submit"] {
    @include primary-path-button;
    width: 100%;
    margin-bottom: 3em;
  }
}

.sign-up-supporting-content {
  text-align: center;
  @include media($large-screen-up) {
    @include shift(1);
    @include span-columns(6 of 12);
    margin-top: 1em;
  }
}
  .sign-up-supporting-content__copy {
    h3 {}
    p {
      .free {}
    }
  }
.sign-up-supporting-content__image {}