#{$all-button-inputs},
button {
  &:focus {
    outline: 0;
  }

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
}

@mixin primary-path-button($color: $brand-green) {
  appearance: none;
  -webkit-font-smoothing: antialiased;
  background-color: $color;
  border-radius: $base-border-radius;
  border: 1px solid $color;
  color: shade($color, 55%);
  text-transform: uppercase;
  cursor: pointer;
  display: inline-block;
  //font-family: $base-font-family;
  font-size: modular-scale(-1, $base-font-size);
  font-weight: 500;
  line-height: 1;
  padding: 1.5em 2.25em;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  transition: all 0.1s linear;
  letter-spacing: 1px;

  &:hover,
  &:focus {
    color: shade(shade($color, 55%), 55%);
    background-color: shade($color, 10%);
    border-color: shade($color, 10%);
  }

  &:active {
    background-color: shade(shade($color, 10%), 10%);
    border-color: shade(shade($color, 10%), 10%);
    transition: all 0s linear;
  }

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
}

@mixin skeleton-button($color: $brand-green) {
  appearance: none;
  -webkit-font-smoothing: antialiased;
  background-color: transparent;
  border-radius: $base-border-radius;
  border: 1px solid $color;
  color: $color;
  cursor: pointer;
  display: inline-block;
  //font-family: $base-font-family;
  //font-size: $base-font-size;
  font-weight: 500;
  text-transform: uppercase;
  padding: .8em 1.1em;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  transition: all 0.1s linear;
  letter-spacing: 1px;

  &:hover,
  &:focus {
    color: shade($color, 55%);
    background-color: $color;
  }

  &:active {
    background-color: shade($color, 10%);
    border-color: shade($color, 10%);
    transition: all 0s linear;
  }

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
}

