/*------------------------------------*\
    #HIDING
\*------------------------------------*/

/*
 * Hide visually and from screen readers:
 */
@include utility('hidden') {
  @include hide;
}

/*
 * Hide only visually, but have it available for screen readers:
 * http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
 */
@include utility('visuallyhidden') {
  @include hide(true);
}

/*
 * Hide visually and from screen readers, but maintain layout:
 */
@include utility('invisible') {
  visibility: hidden;
}

/*
 * Hide text to allow background to show.
 */
@include utility('hide-text') {
  @include hide-text;
}





/*------------------------------------*\
    #NO-BOTTOM-MARGIN
\*------------------------------------*/

@include utility('no-bottom-margin') {
  margin-bottom: 0 !important;
}





/*------------------------------------*\
    #CENTERED-TEXT
\*------------------------------------*/

@include utility('centered-text') {
  text-align: center;
}





/*------------------------------------*\
  #RECOLOR-SVGs
\*------------------------------------*/

@include utility('svg-black') {
  path,
  polygon,
  rect {
    fill: black;
  }
}

@include utility('svg-white') {
  path,
  polygon,
  rect {
    fill: white;
  }
}





/*------------------------------------*\
  #ANCHOR
\*------------------------------------*/

@include utility('anchor') {
  display: block;
  height: ms();
  margin-top: - ms();
  visibility: hidden;

  @include medium-desktop {
    height: $header-height--medium-desktop-scrolled + ms();
    margin-top: - ($header-height--medium-desktop-scrolled + ms());
  }
}
