body {
  font-feature-settings: "kern", "liga", "pnum";
  -webkit-font-smoothing: antialiased;
  color: $base-font-color;
  font-family: $base-font-family;
  font-size: $base-font-size;
  line-height: $base-line-height;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $heading-font-family;
  font-size: $base-font-size;
  line-height: $heading-line-height;
  margin: 0 0 $small-spacing;
  font-weight: 700;
}

h4, h5, h6 {
  font-weight: 500;
}

p {
  margin: 0 0 $small-spacing;
}

a, %a-effect {
  $link-color: shade($action-color, 20%);;
  color: $link-color;
  text-decoration: none;
  //transition: all 0.1s linear;

  &:active,
  &:focus,
  &:hover {
    color: darken($link-color, 15%);
  }

  &:active,
  &:focus {
    outline: none;
  }
}

hr {
  border-bottom: $base-border;
  border-left: none;
  border-right: none;
  border-top: none;
  margin: $base-spacing 0;
}

img,
picture {
  margin: 0;
  max-width: 100%;
}

//
h1, h2, h3, h4, h5, h6 {
  margin: $small-spacing 0 $small-spacing;
}
h1 {
  font-size: modular-scale(4, $base-font-size);
}

h2 {
  font-size: modular-scale(3, $base-font-size);
}

h3 {
  font-size: modular-scale(2, $base-font-size);
}

h4 {
  font-size: modular-scale(1, $base-font-size);
}

h5 {
  font-size: modular-scale(0, $base-font-size);
}

h6 {
  font-size: modular-scale(-1, $base-font-size);
}

ul.default {
  @extend %default-ul;
}
ol.default {
  @extend %default-ol;
}
