.ppc-content {
  @include media($large-screen-up) {
    @include span-columns(7 of 12);
  }
  @include media($xl-screen-up) {
    @include span-columns(7 of 12);
    padding-left: 3em;
  }
}

.ppc-sidebar {
  @include clearfix;
  position: relative;
  margin-bottom: 2em;
  @include media($large-screen-up) {
    @include span-columns(5 of 12);
    margin-bottom: 0;
    margin-top: -19.5em;
    padding-left: 2em;
  }
  @include media($xl-screen-up) {
    margin-top: -19em;
    padding-left: 4em;
  }
  h4 {
    margin-top: 0;
    text-transform: uppercase;
    margin-bottom: 1.5em;
    font-weight: 400;
  }
  .global-testimonial {
    margin-top: 2em;
  }
}

.ppc-main-content {
  position: relative;
  margin-top: 2em;
  padding-bottom: 4em;
  padding-top: 1px;
  .ppc-content>img:first-child {
    display: block;
    margin: -12em auto 3em;
    @include media($large-screen-up) {
      margin-top: -9em;
    }
  }
}

.ppc-sidebar .ppc-form {
  background: lighten($light-gray, 3%);
  padding: 1.5em;
  input[type="submit"] {
    @include primary-path-button;
    font-size: .8em !important;
    display: block;
    width: 100% !important;
  }
  label {
    font-size: .8em !important;
  }
}

.ppc-form__disclaimer {
  font-size: .7em;
  font-style: italic;
  margin-top: 3em;
}

.ppc-sidebar {
  &--no-hero {
    margin-top: 0;

    @include media($large-screen-up) {
      padding-top: 40.8px;
    }
  }
}
