.customers-leaderboard {}
.customers-leaderboard__container {}
.customers-content {}
.customers-content__case-studies {}
  .customers-content__case-studies__container {
    @include span-columns(12 of 12);
    width: 100%;
    display: block;
    float: left;
    margin-bottom: 6em;
    margin-top: 2em;
  }
    .customers-content__case-study__item {
      margin-top: 3em;
      &:first-child {
        margin-top: 0em;
      }
      @include media($medium-screen-up) {
        @include span-columns(4 of 12);
        margin-bottom: 2em;
        @include omega(3n);
        margin-top: 0em;
      }
    }
      .customers-content__case-study__item__logo {
        height: 60px;
        a {
          display: block;
          width: 215px;
        }
        img {
          //max-width: 150px;
          //height: auto;
          max-height: 35px;
          width: auto;
          margin-top: 15px;
        }
      }
      .customers-content__case-study__item__text {
        p {
          font-size: 1.1em;
          line-height: 1.4;
          @include media($medium-screen-up) {
            padding-right: 1em;
            font-size: .9em;
          }
          @include media($large-screen-up) {
            padding-right: 3em;
          }
        }
      }
      .customers-content__case-study__item__link {
        a {
          @include skeleton-button--dark;
          font-size: .75em;
          padding: .75em 1.65em;
        }
      }

.customers {
  .global-content h3 {
    font-size: .9em;
    text-transform: uppercase;
    font-weight: 700;
  }
}

.customers-content__logos {
  padding-top: 2em;
  margin-bottom: 2em;
  h2 {}
}
.customers-content__logos__container {
  display: block;
  clear: both;
  float: left;
  width: 100%;
  margin-top: 2em;
  &:first-of-type {
    margin-top: 0em;
  }
  ul {
    display: block;
    clear: both;
    list-style-type: none;
    list-style-image: none;
    margin: 0;
  }
  li {
    float: left;
    margin-left: 2em;
    margin-bottom: 2em;
  }
}
