.hero {
    .ppc &, .seo &, .kanban &, .agile &, .project-management &, .solutions & {
    min-height: 220px;
    padding: modular-scale(5) 0 modular-scale(2);
    @include media($large-screen-up) {
      min-height: 365px;
      padding: modular-scale(6) 0 modular-scale(4);
    }
    .hero__leaderboard__messaging {
      @include span-columns(4);
      padding-left: 2%;
      @include media($medium-screen-up) {
        @include span-columns(7);
        @include shift(.3);
        padding-left: 2em;
      }
      @include media($large-screen-up) {
        @include span-columns(7.2);
        @include shift(0);
        padding-left: 0em;
      }
      @include media($xl-screen-up) {
        padding-left: 3em;
      }

      h2 {
        @include media($small-screen-up) {
          padding: 0 4em 0 0;
        }
        @include media($medium-screen-up) {
          padding: 0 4em 0 0;
        }
        @include media($xl-screen-up) {
          padding-right: 12em;
          padding-left: 0em;
        }
      }
    }
  }
}

.row {
  @include row;
}

.global-section-navigation__container {
  border-bottom: 1px solid $light-gray;
  padding: 2em 0 0;
  @include span-columns(12 of 12);
}
.global-section-navigation__title {
  float: left;
  h2, .h2 {
    font-weight: 300;
    text-transform: uppercase;
    font-size: modular-scale(2, $base-font-size - .2em);
  }
}

.global-section-navigation__links {
  float: right;
  font-weight: 300;
  ul {
    display: block;
    font-size: modular-scale(0, $base-font-size - .2em);
    margin-top: 2em;
  }
  li {
    float: left;
    margin-left: 2em;
    &:first-child {
      margin-left: 0;
    }
  }
  a {
    color: $dark-gray;
    &:hover {
      color: $medium-gray;
    }
  }
}


.global-content {
  line-height: 1.9;
  h2, h3, h4, h5, h6 {
    margin: 0 0 .7em;
  }
  h2 {
    font-weight: 300;
    font-size: 1.7em;
  }
  h3 {
    font-weight: 300;
    font-size: 1.4em;
  }
  h4 {
    font-size: 1.1em;
  }
  h3, h4, h5, h6 {
    font-weight: 300;
  }
  p {
    margin: 0 0 2em;
  }
  ul {
    list-style-type: disc;
    margin-left: 1em;
    list-style-image: url('/assets/images/icons/arrow_icon_right.png');
  }

  ul, ol, dl {
    margin-bottom: 2em;
  }

  ol {
    li {
      margin-bottom: 1em;
    }
  }

  p, ul, ol {
    font-weight: 300;
    line-height: 1.8;
    color: $body-gray;

    b,
    strong {
      font-weight: bold;
    }
  }
  .btn {
    @include primary-path-button;
  }
  img {
    margin-bottom: 1em;
  }
}

.global-content--single-column {
  @include media($large-screen-up) {
    @include shift(2);
    @include span-columns(8 of 12);
  }
  @include media($xl-screen-up) {
    @include shift(2.5);
    @include span-columns(7 of 12);
  }
  img {
    text-align: center;
    display: block;
    margin: 0 auto;
  }
}

.global-content--single-column--left {
  @include media($large-screen-up) {
    @include shift(0);
  }
  @include media($xl-screen-up) {
    @include span-columns(8 of 12);
    @include shift(0);
  }
}

.global-leaderboard {
  padding: 3em 0;
}
.global-leaderboard__container {
  @include media($medium-screen-up) {
    width: percentage(11/12);
  }
  @include media($large-screen-up) {
    width: percentage(8/12);
  }
  margin: 0 auto;
  text-align: center;
  font-weight: 300;
  h1 {
    font-size: modular-scale(2, $base-font-size + .2em);
    @include media($large-screen-up) {
      font-size: modular-scale(4, $base-font-size - .1em);
    }
    margin: 0;
    font-weight: 300;
  }
  p {
    color: $body-gray;
    margin-top: 2em;
  }
}


.global-testimonial, .global-callout-square {
  background: $white;
  color: $medium-gray;
  text-align: center;
  width: percentage(1/1);
  float: left;
  padding-bottom: 2em;
  border: 1px solid $light-gray;
}

.global-testimonial__container, .global-callout-square__container {
  width: 96%;
  margin: 0 auto;
}

.global-testimonial__blockquote {
  padding: 4em 1em 0em;
  span {
    display: block;
    float: left;
    font-size: .85em;
    color: #555851;
  }
  &:before {
    content: "\201C";
    color: $brand-green;
    display: block;
    font-size: 7em;
    font-weight: 700;
    margin: 0;
    line-height: .2;
  }
}

.global-testimonial__attribution {
  clear: both;
  width: 100%;
  float: left;
  margin-top: 1.5em;
}
.global-testimonial__attribution__headshot {
  margin: 0em 0 .5em;
  text-align: center;
  img {
    border-radius: 50%;
    .ppc-main-content & {
      margin: 0 auto 0;
    }
  }
}
.global-testimonial__attribution__name {
  font-weight: 700;
  text-transform: uppercase;
  color: $brand-green;
}
.global-testimonial__attribution__att-title {
  font-size: .8em;
  img {
    max-width: 90px;
    margin-top: 1.4em;
  }
}

.global-testimonial--orange {
  .global-testimonial__blockquote {

    &:before {
      color: $brand-orange;

    }
  }
  .global-testimonial__attribution__name {
    color: $brand-orange;
  }
  .global-testimonial__attribution__att-title a {
    color: $brand-orange;
    &:hover {
      color: darken($brand-orange, 15%);
    }
  }
}


.try-leankit-cta {
  padding: 3em 0;
  text-align: center;
}

.try-leankit-cta__button {
  .try-leankit__cta {
    @include primary-path-button;
    padding: 1.5em .5em;
  }
  .lite-edition__cta {
    @include skeleton-button($brand-orange);
    font-size: modular-scale(-1, $base-font-size);
    line-height: 1;
    padding: 1.5em .5em;
    .trial {
      //font-size: 1em;
      @include media($small-screen-up) {
        //font-size: 1.3em;
      }
      margin-bottom: .2em;
      display: block;
    }
    small {
      text-transform: none;
      font-weight: 400;
    }
  }
  .trial {
    font-size: 1em;
    @include media($small-screen-up) {
      font-size: 1.2em;
    }
    margin-bottom: .2em;
    display: block;
  }
  small {
    text-transform: none;
    font-weight: 400;
    font-size: 95%;
  }
  span {
    margin-left: 1em;
    margin-right: 1em;
  }
  a {
    @include media($mobile-tablet-only) {
      clear: both;
      width: 100%;
      margin-top: 12px;
      margin-bottom: 12px;
    }
  }
}

.try-leankit-alt-path {
  margin-top: 1em;
  font-size: .9em;
}

.try-leankit__info {
  margin-top: 2em;
  padding: 0 5%;
  @include media($small-screen-up) {
    padding: 0 15%;
  }
  @include media($medium-screen-up) {
    padding: 0 15%;
  }
  @include media($large-screen-up) {
    padding: 0 25%;
  }
  @include media($xl-screen-up) {
    padding: 0 30%;
  }
  p {
    font-size: .8em;
    color: $medium-gray;
  }
  .more-info-tooltip {

  }
}


// Full Width short banner (Product Tour/30 Day Trial) w/ Showcase Graphic
.global-tour-trial-container {
  text-align: center;
  margin: 0 0 2em;
}
.global-tour-trial-cta {
  display: inline-block;
}
.global-tour-trial-cta__image {
  @include media($medium-screen-up) {
    float: left;
  }
  img {
    float: left;
  }
}
.global-tour-trial-cta__callout {
  @include media($medium-screen-up) {
    float: right;
    margin-top: 4em;
  }
  .primary-path-cta {
    margin-bottom: 1em;
  }
  .text-cta {
    color: $medium-gray;
    font-size: .85em;
    a {
      color: $medium-gray;
      &:hover {
        color: $body-gray;
      }
    }
  }
}

.global-image-caption {
  line-height: 1.4;
  font-weight: 300;
  font-style: italic;
  font-size: .8em;
}

// Center elements
.global-center-block {
  text-align: center;
  margin: 2em 0 4em;
}

// Full Width Image Frame
.global__img-block--full-width {
  background-color: $background-gray;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

// Full Width Blockquote
.global-blockquote--full {
  color: $body-gray;

  position: relative;
  margin-top: 0;
  margin-bottom: 2.5em;
  padding-top: 2.5em;
  @include media($small-screen-up) {
    border: 0px solid $light-gray;
    border-width: 0 0 0 1px;
    padding-top: 0;
    padding-left: 2em;
    margin-left: 4.7em;
  }
  &:before {
    content: url('/assets/images/blockquote-quote.png');
    position: absolute;
    top: 0;

    @include media($small-screen-up) {
      left: -4.7em;
    }
  }
  blockquote {
    margin: 0;
    line-height: 1.6;
    font-weight: 300;
    font-size: .95em;
  }
  cite {
    margin-top: 2em;
    margin-bottom: 2em;
    display: block;
    font-style: normal;
    .cite__name {
      text-transform: uppercase;
      font-weight: bold;
      color: $brand-green;
      font-size: 1.2em;
    }
    .cite__context {
      font-size: .9em;
      margin-top: -.3em;
    }
  }
}
