.about-section-navigation {
  margin-bottom: 2em;
}
.about-us-founders {
  background: $background-gray;
  margin-bottom: 3em;

  .global-section-navigation__container {
    margin-bottom: 2em;
  }
}

.about-us-founders__founder-box {
  @include span-columns(4 of 4);
  @include media($large-screen-up) {
    @include span-columns(6 of 12);
  }

  line-height: 1.5;
  margin-bottom: 2em;
  p {
    margin-bottom: 0;
  }
}
.about-us-founders__headeshot {
  margin-bottom: 1em;
  img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
  }
}
.about-us-founders__name {
  text-transform: uppercase;
  font-size: .95em;
  p {
    color: $brand-green;
    font-weight: 700;
  }
}
.about-us-founders__title {
  font-size: .8em;
}
.about-us-founders__bio {
  font-size: .8em;
  margin-top: 1em;
  line-height: 1.3;
  @include media($large-screen-up) {
    padding-right: 3em;
  }
}

.about {
  .why-leankit {
    padding-top: 0;
  }
}

.why-leankit-content {
  h2 {
    position: relative;
    img {
      float: left;
      position: relative;
      width: 46px;
      height: 46px;
      top: -6px;
      left: -1px;
      @include media($large-screen-up) {
        width: 72px;
        height: 72px;
        position: absolute;
        top: -20px;
        left: -78px;
      }
    }
  }
}
