.hero {
  color: $white;
  @include clearfix;
  background: url("/assets/images/home_hero_alt.jpg") center no-repeat;
  background-size: cover;
  height: auto;
  position: relative;
  padding-top: 60px;
  margin-bottom: 2.5em;
  @include media($small-screen-up) {
    background: url("/assets/images/home_hero_alt.jpg") center center no-repeat;
    background-size: cover;
  }
  .hero__leaderboard {
    @extend %wrapper;
  }
  .hero__leaderboard__messaging {
    @include span-columns(3.3);
    @include media($small-screen-up) {
      @include span-columns(3.3);
    }
    @include media($medium-screen-up) {
      @include span-columns(8);
      text-align: center;
    }

    @include media($large-screen-up) {
      @include span-columns(12);
      padding: 5em 0;
      padding-left: 0;

      .ppc & {
        padding-bottom: 0;
        padding-top: 0;
      }
    }

    b,
    strong {
      font-weight: bold;
    }

    .ppc & {
      text-align: left;
    }
  }
  .ppc & {
    background-position: center;
  }
  .index & {
    margin-bottom: 0;
  }
}
.home_messaging_ab--hero {
  @include media($medium-screen-up) {
    background-image: url("/assets/images/home_hero_alt2.jpg");
    background-position: right;
  }
  .hero__leaderboard {
    padding-bottom: 2em;
    padding-top: 2em;
  }
  .hero__leaderboard__messaging {
    h2 {
      margin: .5em 0 1.5em;
    }

    @include media($medium-screen-up) {
      @include span-columns(8);
      text-align: left !important;
      width: 50% !important;
      h2 {
        margin: .5em 0 1.5em;
        width: 90%;
      }
    }

    @include media($large-screen-up) {
      @include span-columns(8);
    }
  }

}

.hero-trustmarks {
  background:
      /* top, transparent red, faked with gradient */
      linear-gradient(
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4)
      );
  .global-trustmarks & {
    background: transparent;
  }
}
.hero__trustmarks {
  height: 90px;
  z-index: 9999;
  background: url("/assets/images/logos-new-b-mobile.png") center center no-repeat;
  background-size: 300px auto;
  @include media($small-screen-up) {
    background: url("/assets/images/logos-new-b.png") center center no-repeat;
    background-size: contain;
    height: 80px;
    .global-trustmarks & {
      background: url("/assets/images/logos-new-b-mobile.png") center center no-repeat;
      background-size: contain;
      height: 100px;
    }
  }
  @include media($medium-screen-up) {
    height: 120px;
    background: url("/assets/images/logos-new-b.png") center center no-repeat;
    background-size: contain;
    .global-trustmarks & {
      height: 120px;
      background: url("/assets/images/logos-new-b.png") center center no-repeat;
      background-size: contain;
    }
  }
  @include media($large-screen-up) {
    height: 120px;
    background-size: 1024px auto;
    .global-trustmarks & {
      background-size: 990px auto;
    }
  }
}

.ie9-nb .hero__trustmarks {
  background: url("/assets/images/logos-new-b.png") center center no-repeat;
 }

.hero h1 {
  font-weight: 700;
  font-size: modular-scale(2, $base-font-size);
  text-transform: uppercase;
  margin-bottom: 0;
  margin-top: 0;
  line-height: 1.1;
  @include media($small-screen-up) {
    font-size: modular-scale(2, $base-font-size);

    .index & {
      padding-right: 15% !important;
    }
  }
  @include media($medium-screen-up) {
    font-size: modular-scale(3, $base-font-size) !important;
    padding-right: 0em !important;
  }
  @include media($large-screen-up) {
    font-size: modular-scale(3, $base-font-size) !important;
  }
  @media screen and (max-width: 319px) {
    font-size: 25px;
  }
  @media screen and (min-width: 320px) and (max-width: 479px) {
    font-size: 8vw;
  }
}

.hero h2 {
  margin: .5em 0 0;
  font-weight: 300;
  font-size: modular-scale(1, $base-font-size * .8);
  line-height: 1.2;

  .ppc & {
    font-size: 1.333em;
  }

  @include media($small-screen-up) {
    font-size: modular-scale(1, $base-font-size);
  }
  @include media($medium-screen-up) {
    padding-right: 0em;
  }
  @include media($large-screen-up) {
    clear: both;
    float: none;
    width: 44%;
    margin: .5em auto 0;

    .ppc & {
      width: auto;
    }
  }
}

.hero__leaderboard__messaging__cta {
  clear: both;
  float: none;
  margin-bottom: 2em;
  a {
    @include primary-path-button();
  }
}

// Taken from http://embedresponsively.com
.hero__leaderboard-embed-container {
  height: 0;
  max-width: 100%;
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;

  embed,
  iframe,
  object {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
}

.solutions {
  .hero.hero--with-video {
    padding: 5em 0 1em;

    @include media($medium-screen-up) {
      padding-top: 7em;
    }

    .hero__leaderboard {
      max-width: 560px;
      position: relative;

      @include media($large-screen-up) {
        max-width: 600px;
      }
    }

    .hero__leaderboard__messaging {
      margin-left: 0;
      margin-right: 0;
      width: 100%;

      @include media($large-screen-up) {
        padding-top: 0;
      }

      h1 {
        font-size: 1.5em !important;

        @include media($small-screen-up) {
          padding-right: 0 !important;
        }
      }
    }
  }
}
