.compare-editions-leaderboard__container {
  p {
    margin-top: .7em;
    font-size: 1.3em;
  }
}

.compare-editions-tables {
  background: tint($background-gray, 40%);
  padding: 3em 0;
  .section-title {
    text-align: left;
    h3 {
      margin-bottom: .3em;
      font-size: 1.1em;
      @include media($large-screen-up) {
        font-size: 1.4em;
      }
    }
  }
  .edition {
    text-transform: uppercase;
    font-weight: 500;
    font-size: .8em;
    text-align: center;
    vertical-align: bottom;
    width: 90px;
    @include media($medium-screen-up) {
      width: 90px;
    }
    @include media($large-screen-up) {
      width: 110px;
    }
  }

  .feature {
    font-size: .9em;
    span {
      float: left;
    }

    .more-info-tooltip {
      display: none;
      float: right;
      @include media($medium-screen-up) {
        display: inline-block;
      }
      @include media($large-screen-up) {
        margin-right: 2em;
      }
      @include media($xl-screen-up) {
        margin-right: 15em;
      }
    }
  }

  .check {
    text-align: center;
    span {
      background: url('/assets/images/icons/check_fill_icon.svg') center center no-repeat;
      background-size: 22px 22px;
      height: 28px;
      width: 22px;
      display: inline-block;
      background-position: 0px 6px, center;
    }
  }

  .size {
    text-align: center;
    &.two-lines {}
  }

  .text-item {
    text-align: center;
    font-size: .9em;
    font-weight: 400;
  }
}
