.pricing-leaderboard {
  padding: 3em 0 3em;
}
.pricing-leaderboard__container {
  p {
    margin-top: .7em;
    font-size: 1.3em;
  }
}

.pricing-tables__package {
  padding: 2em 0 3em;
  @include span-columns(4);
  @include media($medium-screen-up) {
    width: percentage(6/12);
    margin-top: 0em;
    margin-right: 0;
  }
  @include media($large-screen-up) {
    margin-top: 0em;
    width: percentage(3/12);
    margin-right: 0;
  }
  @include media($xl-screen-up) {
    width: percentage(3/12);
    margin-right: 0;
  }
}

.pricing-tables {
  text-align: center;
  ul, p {
    line-height: 1.6 !important;
  }
  ul {
    list-style-type: none;
    margin-left: 0em;
    list-style-image: none;
    margin-bottom: 0em;
  }
  .plan-name {
    font-size: 1.4em;
    text-transform: uppercase;
    font-weight: 700;
    color: $brand-green;
    margin-bottom: 1em;
  }
  .price {
    line-height: 1.2;
    color: $dark-gray;
    strong {
      font-size: 1.6em;
    }
    small {
      color: $medium-gray;
    }
  }
  .trial-button {
    margin: 2em 0;
    @include media($medium-screen-up) {
      margin: 0em 0;
      height: 86px;
      padding-top: 1em;
    }
    a {
      @include primary-path-button;
    }
  }
  .plan-description {
    font-size: .9em;
    margin-top: 1.5em;
    @include media($medium-screen-up) {
      padding: 0 5em;
    }
    @include media($large-screen-up) {
      padding: 0 1em;
      height: 50px;
    }
    @include media($xl-screen-up) {
      padding: 0 2.4em;
    }
  }
  .feature-intro {
    font-weight: 500;
    margin-top: 2em;
    font-size: .8em;
    @include media($xl-screen-up) {
      font-size: 1em;
    }
    em {
      font-weight: 300;
    }
  }
  .feature {
    margin-top: .4em;
    font-size: .9em;
    font-weight: 400;
    &:first-child {
      margin-top: 0;
    }
  }
  .blank {
    display: none;
    @include media($medium-screen-up) {
      display: block;
      height: 23px;
      margin-top: .4em;
    }
  }

  .more-info-tooltip.more-info-tooltip--text {
    color: $medium-gray;
    &:after {
      color: #bbb;
      content: "\2b";
      position: relative;
      top: -2px;
      left: 2px;
      display: inline-block;
      font-family: 'Glyphicons Halflings';
      font-style: normal;
      font-weight: 400;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
  }

  .more-info-tooltip.more-info-tooltip--text .more-info-tooltip__tip {
    left: -6em;
  }
  .featured-plan {
    background: $background-gray;
    position: relative;
    margin: 2em 0;
    @include media($medium-screen-up) {
      margin: 0 0;
    }
    &:before {
      content: "Most Popular";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      color: $light-gray;
      font-weight: 400;
      background: $dark-gray;
      padding: .5em 0;
      top: -2em;
    }
  }
  .pricing-tables__package {
    border-bottom: 1px solid $light-gray;
    @include media($medium-screen-up) {
      border-bottom: 0;
    }
  }
}

// Based on Optimizely tests
.pricing-tables {
  @include media($medium-screen-up) {
    padding-bottom: 2em;
  }

  .pricing-tables__package {
    @include media($medium-screen-up) {
      border-bottom-width: 0;
      float: none;
      vertical-align: top;
      display: inline-block;
      position: relative;
      min-height: 620px;
      width: 32%;
    }

    @include media($large-screen-up) {
      width: 25%;
      min-height: 652px;
    }

    &.pricing-tables__package--plan3 {
      @include media($medium-screen-up) {
        background-color: #deffd1;
        border-left: 1px solid #d7d9d6;
        border-right: 1px solid #d7d9d6;
      }
    }

    .plan-button {
      margin-top: 0.5em;

      @include media($medium-screen-up) {
        margin-top: 1.5em;
      }
    }

    .recommended-ribbon {
      @include media($medium-screen-up) {
        position: absolute;
        top: -18px;
        left: 0;
        right: 0;
        font-weight: 700;
        font-size: 16px;
      }

      &:after {
        @include media($medium-screen-up) {
          content: '';
          border-top: 18px solid #96c93d;
          border-right: 16px solid transparent;
          border-bottom: 18px solid #96c93d;
          display: inline-block;
          vertical-align: top;
        }
      }

      &:before {
        @include media($medium-screen-up) {
          content: '';
          border-top: 18px solid #96c93d;
          border-left: 16px solid transparent;
          border-bottom: 18px solid #96c93d;
          display: inline-block;
          vertical-align: top;
        }
      }

      span {
        @include media($medium-screen-up) {
          display: inline-block;
          line-height: 36px;
          background-color: #96c93d;
          color: #ecffd3;
          vertical-align: top;
          padding-left: 8px;
          padding-right: 8px;
        }

      }
    }
  }
}
.try-leankit-cta {
  .pricing &,
  .compare-editions &,
  .customers & {
    @include media($medium-screen-up) {
      display: none;
    }
  }
}
