fieldset {
  background-color: lighten($base-border-color, 10%);
  border: $base-border;
  margin: 0 0 $small-spacing;
  padding: $base-spacing;
}

input,
label,
select {
  display: block;
  font-family: $base-font-family;
  font-size: $base-font-size;
}

label {
  font-weight: 600;
  margin-bottom: $small-spacing / 2;

  &.required::after {
    content: "*";
  }

  abbr {
    display: none;
  }
}

#{$all-text-inputs},
select[multiple=multiple],
textarea {
  background-color: $base-background-color;
  border: $base-border;
  border-radius: $base-border-radius;
  box-shadow: $form-box-shadow;
  box-sizing: border-box;
  font-family: $base-font-family;
  font-size: $base-font-size;
  margin-bottom: $base-spacing / 2;
  padding: $base-spacing / 3;
  transition: border-color;
  width: 100%;

  &:hover {
    border-color: darken($base-border-color, 10%);
  }

  &:focus {
    border-color: $medium-gray;
    //box-shadow: $form-box-shadow-focus;
    outline: none;
  }
}

textarea {
  resize: vertical;
}

input[type="search"] {
  appearance: none;
}

input[type="checkbox"],
input[type="radio"] {
  display: inline;
  margin-right: $small-spacing / 2;
}

input[type="file"] {
  padding-bottom: $small-spacing;
  width: 100%;
}

select {
  margin-bottom: $base-spacing;
  max-width: 100%;
  width: auto;
}









// Eloqua Overrides
.form-design-field, .field-wrapper, ._100, .field-wrapper p, .field-wrapper label {
  left: auto !important;
  right: auto !important;
  bottom: auto !important;
  top: auto !important;
  //float: none !important;
  //width: 100% !important;
  clear: none !important;
  //margin: 0 !important;
  //position: static !important;
}

._100 {
  position: relative;
}

form {
  label {
    position: static !important;
    font-weight: 400;
    margin: 1em 0 .2em !important;
    font-size: 1em !important;
    padding: inherit !important;
    line-height: auto !important;
    white-space: normal !important;
    span {
      display: none !important;
    }
  }
}

label.sr-only {
  margin: 0 !important;
}

.LV_validation_message {
  font-weight: 400 !important;
  margin: 0 0 0 5px;

  //position: absolute;
  top: 8px;
  right: 8px;
  display: none;
}

input[name="emailAddress"] + .LV_validation_message {
  display: block;
  text-align: right;
  height: 0;
  margin-top: -81px;
  padding-bottom: 81px;
  margin-right: 10px;
}

.LV_valid {
  color:#00CC00;
  display:none;
}

.LV_invalid {
  color:#ef625f !important;
  font-size: .85em !important;
  &:hover {
    cursor: text;
  }
}

.LV_valid_field,
input.LV_valid_field:hover,
input.LV_valid_field:active,
textarea.LV_valid_field:hover,
textarea.LV_valid_field:active {
  border: 1px solid $base-border-color !important;
}

.LV_invalid_field,
input.LV_invalid_field:hover,
input.LV_invalid_field:active,
textarea.LV_invalid_field:hover,
textarea.LV_invalid_field:active {
  border: 1px solid #ef625f !important;
}
// END Eloqua Overrides

