.lbr-promo {
  color: $white;
  @include clearfix;
  background: url("/assets/images/lbr/promo_bg.jpg") center center no-repeat;
  background-size: cover;
  height: auto;
  padding-top: 3em;
  position: relative;
  @include media($large-screen-up) {
    padding-top: 0;
  }
  &--trustmarks {
    padding-top: 6em;
  }
  &__container {
    @include clearfix;
    overflow: hidden;
  }
  &--trustmarks &__container {
    min-height: 380px;
  }
  &__cta {
    clear: both;
    float: none;
    margin-bottom: 2em;
    a {
      @include primary-path-button();
    }
  }
  &__leaderboard-inner {
    max-width: 350px;
    @include media($large-screen-up) {
      margin-left: 5%;
    }
    @include media($xl-screen-up) {
      margin-left: 25%;
    }
  }
  &__leaderboard {
    padding: 0 15px;
    @include media($medium-screen-up) {
      @include span-columns(5 of 12);
    }
    @include media($large-screen-up) {
      @include span-columns(5 of 12);
      margin-bottom: 40px;
      margin-top: 80px;
    }
    @include media($xl-screen-up) {
      margin-bottom: 70px;
      margin-top: 100px;
    }
    h1 {
      color: $brand-green;
      font-size: 2.3em;
      letter-spacing: .05em;
      line-height: 1.1em;
      margin-bottom: .25em;
      margin-top: 0;
      text-transform: uppercase;
      @include media($small-screen-up) {
        font-size: 2.5em;
      }
      .light {
        font-weight: 300;
      }
    }
    p {
      margin-bottom: 1.2em;
    }
  }
  &--trustmarks &__leaderboard {
    @include media($large-screen-up) {
      margin-bottom: 0;
    }
    @include media($xl-screen-up) {
      margin-top: 80px;
    }
  }
  &__mobile {
    @include media($large-screen-up) {
      @include span-columns(2 of 12);
      margin-top: 40px;
    }
    img {
      display: none;
      @include media($large-screen-up) {
        display: inline;
        margin-left: -80%;
        max-width: 180%;
      }
      @include media($xl-screen-up) {
        margin-left: -30%;
      }
    }
  }
  &__print {
    margin-bottom: -70px;
    text-align: right;
    @include media($small-screen-up) {
      margin-bottom: -90px;
    }
    @include media($medium-screen-up) {
      @include span-columns(7 of 12);
    }
    @include media($large-screen-up) {
      @include span-columns(5 of 12);
      height: 340px;
      margin-top: 25px;
    }
    img {
      max-width: 170%;
      @include media($small-screen-up) {
        max-width: 125%;
      }
      @include media($medium-screen-up) {
        max-width: 180%;
      }
    }
  }
  &--trustmarks &__print {
    height: auto;
    margin-bottom: 0;
    overflow: inherit;
  }

  .hero-trustmarks {
    background:
        /* top, transparent red, faked with gradient */
        linear-gradient(
          rgba(0, 0, 0, 0.1),
          rgba(0, 0, 0, 0.1)
        );
    margin-top: -50px;
    position: relative;
    @include media($small-screen-up) {
      margin-top: -55px;
    }
    @include media($medium-screen-up) {
      margin-top: -60px;
    }
    @include media($large-screen-up) {
      margin-top: -50px;
    }
    @include media($xl-screen-up) {
      margin-top: -70px;
    }
  }
}
