/*------------------------------------*\
  #HEADINGS
\*------------------------------------*/


/*
 * Much of the thought behind the naming scheme was pulled from
 * https://csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/
 */

@mixin heading-reset() {
  font-family: $gotham;
  font-size: ms();
  font-weight: $semibold-weight;
  line-height: 1.3;
  margin: 0;
  text-transform: uppercase;
}

.h-alpha {
  @include heading-reset;
  font-size: ms(3);
  margin-bottom: ms(3);

  @include large-mobile {
    font-size: ms(5);
  }
}

.h-beta {
  @include heading-reset;
  font-size: ms(2);
  margin-bottom: ms(3);

  @include large-mobile {
    font-size: ms(3);
  }
}

.h-gamma,
.s-content h2 {
  @include heading-reset;
  font-size: ms(1);
  margin-bottom: ms(-1);

  @include large-mobile {
    font-size: ms(2);
  }
}

.h-delta,
.s-content h3 {
  @include heading-reset;
  font-size: ms(0);
  margin-bottom: ms(-2);

  @include large-mobile {
    font-size: ms(1);
  }
}

.h-epsilon,
.s-content h4 {
  @include heading-reset;
  font-size: ms(-1);
  margin-bottom: ms(-2);

  @include large-mobile {
    font-size: ms(0);
  }
}





/*------------------------------------*\
  #COPY
\*------------------------------------*/

@mixin body-copy() {
  color: $body-copy;
  font-size: ms();
  font-weight: $light-weight;
  line-height: 1.8;
}

@include scope('content') {
  @include body-copy;

  a, {
    color: $link;
    text-decoration: underline;

    &:focus,
    &:hover {
      color: $link--hover;
    }

    &:active {
      color: $link--active;
    }

    &:visited {
      color: $link--visited;
    }
  }

  h2 {
    margin-top: ms(5);
  }

  h3 {
    margin-top: ms(4);
  }

  h4 {
    margin-top: ms(3);
  }

  ol {
    list-style-type: decimal;
    margin: ms() 0;
    padding-left: ms(3);

    li {
      line-height: ms(3);
    }
  }

  ul {
    list-style-image: url(/assets/images/icons/arrow_icon_right.png);
    margin: ms() 0;
    padding-left: ms(3);

    li {
      line-height: ms(3);
    }
  }

  ol, ul, li {
    margin-bottom: ms();
  }

  p {
    margin-bottom: ms();
  }
}

@include component('copy') {
  @include body-copy;

  @include modifier('caption') {
    font-size: ms(-2);
    line-height: 1.5;
  }
  @include modifier('feature') {
    &,
    p {
      color: black;
      font-size: ms(3);
      line-height: 1.6;
      margin-bottom: ms(2);
    }
  }

  @include modifier('hero') {
    font-size: ms(1);
    font-weight: $light-weight;
    line-height: 1.4;
  }

  @include modifier('meta') {
    font-size: ms(-1);
    line-height: 1.5;
  }

  @include modifier('small') {
    font-size: ms(-3);
    line-height: 1.5;
  }
}
