.leankit-blog {
  .global-content--single-column {
    margin-bottom: 3em;
  }
  #searchform {
    .search-input {
      float: left;
    }
    #searchsubmit {
      float: left;
    }
  }

  article.blog-post {
    border-bottom: 1px solid $light-gray;
    margin-bottom: 4em;
    padding-bottom: 2em;
  }

  .page-header h3 {
    margin-top: 1.5em;
  }

  .blog-post-title {
    font-weight: 300;
    font-size: 2.2em;
    margin-top: 1em;
    a {
      color: $dark-gray;
      &:hover {
        color: tint($dark-gray, 20%);
      }
    }
  }

  .comment-list {
    .comment-author {
      img {
        text-align: left;
        display: inline-block;
        margin: 0 1em 0 0;
      }
    }
  }

  /*
    ****
    ** WP classes
    ****
  */

  .updated{display:none}
  /*--------------------------------------------------------------
  7.0 Alignments
  --------------------------------------------------------------*/

  $aligned-image-margin-top:        .3em;
  $aligned-image-margin-side:       1.5em;

  .alignleft, .alignright {
    margin-top: .5em !important;
  }

  .alignleft {
  	display: inline;
  	float: left;
  	margin-right: .5em;
  	@include media($small-screen-up) {
    	margin-right: $aligned-image-margin-side;
    }
  }
  .alignright {
  	display: inline;
  	float: right;
  	margin-left: .5em;
  	@include media($small-screen-up) {
    	margin-left: $aligned-image-margin-side;
    }
  }
  .aligncenter, .alignnone {
  	clear: both;
  	display: block;
  	margin: 0 auto;
  	width: auto !important;
  	img {
  	  width: auto !important;
  	  margin: 0 auto;
  	}
  }
  img {
    height: auto;
  }



  /*--------------------------------------------------------------
  12.0 Media
  --------------------------------------------------------------*/
  .page-content img.wp-smiley,
  .entry-content img.wp-smiley,
  .comment-content img.wp-smiley {
  	border: none;
  	margin-bottom: 0;
  	margin-top: 0;
  	padding: 0;
  }
  .wp-caption {
  	border: 1px solid #ccc;
  	margin-bottom: .8em;
  	max-width: 100%;
    margin-bottom: 2em;
  	@include media($handheld-only) {
      &.alignleft, &.alignright {
        max-width: 40%;
      }
    }
  }
  .wp-caption img[class*="wp-image-"] {
  	display: block;
  	margin: 0% auto 0;
  	max-width: 100%;
  }
  .wp-caption-text {
  	text-align: center;
  	font-size: .75em;
  	padding: 0 3px;
  	font-weight: 400;
  	@include media($handheld-only) {
      text-align: left;
      font-size: .7em;
      word-wrap: break-word;
    }
  }
  .wp-caption .wp-caption-text {
  	margin: 0.8075em 0 0;
  }
  .site-main .gallery {
  	margin-bottom: 1.5em;
  }
  .gallery-caption {
  }
  .site-main .gallery a img {
  	border: none;
  	height: auto;
  	max-width: 90%;
  }
  .site-main .gallery dd,
  .site-main .gallery figcaption {
  	margin: 0;
  }
  .site-main .gallery-columns-4 .gallery-item {
  }
  .site-main .gallery-columns-4 .gallery-item img {
  }
}

/*--------------------------------------------------------------
11.0 Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .paging-navigation, /* Older / Newer Posts Navigation (always hidden) */
.infinite-scroll.neverending .site-footer { /* Theme Footer (when set to scrolling) */
	display: none !important;
}
/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before */
.infinity-end.neverending .site-footer {
	display: block;
}

#infinite-handle {
  .btn {
    width: 100%;
    text-align: center;
  }
}
