.seo, .kanban, .agile, .project-management, .solutions {
  .hero {
    text-align: center;
    .hero__leaderboard__messaging {
      padding: 0em;
      @include media($large-screen-up) {
        @include span-columns(8);
        @include shift(2);
        padding: 4em 0 6em;
      }
      @include media($xl-screen-up) {
        @include span-columns(8);
        @include shift(2);
        padding: 4em 0 6em;
      }
    }
  }

  .seo-main-content__container, .seo-main-content__more-reading__container {
    @include media($large-screen-up) {
      @include shift(2);
      @include span-columns(8 of 12);
    }
    @include media($xl-screen-up) {
      @include shift(2.5);
      @include span-columns(7 of 12);
    }
    img {
      text-align: center;
      display: block;
      margin: 0 auto;
    }
    .seo-main-content__full-image {
      margin: 2em 0 4em;
      img {
        @include media($large-screen-up) {
          @include shift(-1 of 7);
          max-width: percentage(9/7);
        }
      }
    }
    .seo-main-content__image__caption {
      line-height: 1.4;
      font-weight: 300;
      font-style: italic;
      font-size: .8em;
    }
  }
}

.seo-main-content {
  margin-bottom: 2em;
}

.seo-main-content__more-reading {
  background: $background-gray;
  text-transform: uppercase;
  padding: 3em 0 4em;
  h3 {
    color: $medium-gray;
    font-size: .85em;

    font-weight: 400;
  }
  ul {
    list-style: none;
  }
  li {
    border-top: 1px solid $medium-gray;
    margin-top: 1em;
    padding-top: 1em;
    font-size: 1.3em;
    &:first-child {
      border-top: 1px solid transparent;
      padding-top: 0em;
    }
  }
  h3, li {
    padding-left: 30px;
    padding-right: 30px;
  }

  a {
    color: $dark-gray;
    font-weight: 700;
    &:hover {
      color: tint($dark-gray, 20%);
    }
  }
}
