/*------------------------------------*\
 * #BREAKPOINTS
\*------------------------------------*/

@mixin mobile-only() {
  @media screen and (max-width: #{$large-mobile - 1}) {
    @content;
  }
}

@mixin large-mobile($only: null) {
  $min: 'and (min-width: #{$large-mobile})';
  $max: '';

  @if $only == 'only' {
    $max: 'and (max-width: #{$small-tablet - 1})';
  }

  @media screen #{$min + $max} {
    @content;
  }
}

@mixin small-tablet($only: null) {
  $min: 'and (min-width: #{$small-tablet})';
  $max: '';

  @if $only == 'only' {
    $max: 'and (max-width: #{$tablet - 1})';
  }

  @media screen #{$min + $max} {
    @content;
  }
}

@mixin tablet($only: null) {
  $min: 'and (min-width: #{$tablet})';
  $max: '';

  @if $only == 'only' {
