@mixin x-large-desktop() {
  @media screen and (min-width: $x-large-desktop) {
    @content;
  }
}

// Breakpoint at which padded container's content is at max width
@mixin max-padded-container() {
  @media screen and (min-width: $container-max-box-width) {
    @content;
  }
}

// Breakpoint at which padded narrow container's content is at max width
@mixin max-padded-container--narrow() {
  @media screen and (min-width: $container-max-box-width--narrow) {
    @content;
  }
}





/*------------------------------------*\
    #ACCESSIBILITY
\*------------------------------------*/

/*
 * If $accessible == true, hide only visually, but have it available for
 * screen readers:
 * http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
 *
 * Otherwise, hide visually and from screen readers.
 */

@mixin hide($accessible: false) {
  @if $accessible == false {
    display: none !important;
  } @else {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
}





/*------------------------------------*\
 * #QUANTITY-QUERIES
\*------------------------------------*/

@mixin qq($count, $operator: '=') {
  @if is_numeric($count) == false {
    @error "The qq() function requires a numeric parameter.";
  }

  @if $count == 1 {
    &:only-child {
      @content;
    }
  } @else if $operator == '=' {
    &:nth-last-child(#{$count}):first-child,
    &:nth-last-child(#{$count}):first-child ~ & {
      @content;
    }
  } @else if $operator == '>=' {
    &:nth-last-child(n+#{$count}),
    &:nth-last-child(n+#{$count}) ~ & {
      @content;
    }
  } @else if $operator == '<=' {
    &:nth-last-child(-n+#{$count}):first-child,
    &:nth-last-child(-n+#{$count}):first-child ~ & {
      @content;
    }
  }
}
