.case-study-leaderboard {
  background: url('/assets/images/customers/case-study/case-study-header-tr.jpg') center center no-repeat;
  background-size: cover;
}

.case-study-leaderboard__container {
  padding: 2em 0 3em;
  h1 {
    color: $white;
    margin-top: 0;
    display: block;
  }
}
.case-study-leaderboard__logo {
  img {
    max-width: 322px;
    vertical-align: middle;
    @include media($medium-screen-up) {
      position: relative;
      right: 1.5em;
      //top: .7em;
    }
    @include media($large-screen-up) {
      //top: .5em;
    }
    @include media($xl-screen-up) {
      //top: .5em;
    }
  }
}
.case-study-leaderboard__text {
  clear: both;
  display: block;
  @include media($medium-screen-up) {
    clear: none;
    display: initial;

    border-left: 1px solid $white;
    padding: 1.5em 0em 2em 1.5em;
  }
}
.case-study__body {
  padding: 3em 0;
}
.case-study__body__intro {}
.case-study__body__profile {}
  .case-study__body__highlights {
    @include row;
  }
  .case-study__body__highlights__item {
    margin-top: 2em;
    @include span-columns(4);
    @include media($large-screen-up) {
      @include span-columns(4 of 12);
      margin-top: 0em;
    }
    &:first-child {
      margin-top: 0em;
    }
  }
  .case-study__body__highlights__item__icon {
    width: percentage(1/12);
    float: left;
    text-align: center;
    @include media($large-screen-up) {
      width: percentage(12/12);
      clear: both;
    }
    img {
      margin: 0 auto;
      display: block;
    }
  }
  .case-study__body__highlights__item__text {
    width: percentage(10.5/12);
    margin-left: percentage(.5/12);
    float: left;
    padding-right: 1em;
    @include media($large-screen-up) {
      width: percentage(12/12);
      clear: both;
      margin-left: 0;
      margin-top: 1em;
      padding-right: .3em;
      padding-left: .3em;
      text-align: center;
    }
    p {
      @include media($large-screen-up) {
        font-size: .8em;
        line-height: 1.3;
      }
    }
  }
  .case-study__body__interviewee {
    margin-top: 1em;
    display: block;
    float: left;
    p {}
  }
.case-study__body__qa {}
  .case-study__body__qa__spacer {
    padding-top: 2em;
    margin-bottom: 4em;
  }
  .case-study__body__qa_q {
    p {
      color: $brand-blue;
      font-weight: 400;
      font-size: 1.2em;
    }
  }
  .case-study__body__qa_a {
    blockquote {
      border-left: 4px solid $brand-blue;
      padding-left: 2.5em;
      margin-top: 2em;
      margin-bottom: 2em;
      display: block;
      margin-left: 0;
      margin-right: 0;
      font-weight: 300;
      font-style: italic;
      font-size: 1.4em;
      padding-top: 1em;
      padding-bottom: 1em;
      line-height: 1.4;
    }
  }
  .case-study__body__img--plus-caption {
    margin-bottom: 1em;
    .full {
      img {
        @include media($xl-screen-up) {
          max-width: percentage(14/12);
          @include shift(-1);
        }
      }
    }
    .right {
      @include media($medium-screen-up) {
        width: auto;
        float: right;
        margin-left: 1.5em;
      }
      p {
        margin-bottom: 0;
      }
    }
    img {

    }
    .img-caption {
      margin-top: .1em;
    }
  }

  .case-study__body__attribute {
    font-weight: 700;
    text-align: center;
    font-size: 1.2em;
    margin-top: 4em;
  }
